<template>
  <div class="q-pa-md q-gutter-sm">
    <q-tree
      :nodes="models"
      node-key="unique_code"
      label-key="name"
      @update:selected="selectaaa"
      :selected.sync="selected"
      children-key="subset"
    />
  </div>
</template>

<script>
export default {
  name: "model",
  data() {
    return {
      selected: "",
      models: [],
    };
  },
  mounted() {
    this.loadModels();
  },
  methods: {
    selectaaa(e) {
      let tmp = e;
      console.log(e.length);
      if (e.length !== 3) {
        if (e.length !== 5) {
          this.$emit("modelChange", {
            category_unique_code: "",
            entire_model_unique_code: "",
            model_unique_code: tmp,
          });
        } else {
          this.$emit("modelChange", {
            category_unique_code: "",
            entire_model_unique_code: tmp,
            model_unique_code: "",
          });
        }
      } else {
        this.$emit("modelChange", {
          category_unique_code: tmp,
          entire_model_unique_code: "",
          model_unique_code: "",
        });
      }
    },
    loadModels() {
      const notif = this.$q.notify({
        type: "ongoing",
        message: "正在获取种类型数据...",
        position: "top",
      });
      this.$http
        .get("types")
        .then((res) => {
          let tmp = res.data.data;
          this.models = tmp;
          notif({
            message: "获取种类型信息成功",
            position: "top",
            type: "positive",
          });
        })
        .catch(({ response }) => {
          notif({
            message: response.data.msg,
            position: "top",
            type: "warning",
          });
        });
    },
  },
};
</script>

