<template>
  <div>
    <h1>测试安卓接口功能</h1>
    <h2>{{ date }}</h2>
    <div class="q-pa-md">
      <q-table title="缓存" :data="data" :columns="columns" row-key="name" />
    </div>
    <div class="q-pa-md column items-center">
      <q-btn
        color="primary"
        label="测试-复位Web端首页"
        @click="testReset"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-清空Web端缓存"
        @click="testClear"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-更新Web端缓存"
        @click="testUpdate"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-参数-所有"
        @click="testParameterAll"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-参数-清空"
        @click="testParameterClear"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-参数-查询"
        @click="testParameterQuery"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-参数-删除"
        @click="testParameterDelete"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-参数-插入"
        @click="testParameterInsert"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-参数-更新"
        @click="testParameterUpdate"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-缓存-写入"
        @click="testCachePut"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-缓存-读取"
        @click="testCacheGet"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-缓存-数量"
        @click="testCacheSize"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-缓存-去除"
        @click="testCacheRemove"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-缓存-清空"
        @click="testCacheClear"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-CAD-访问"
        @click="testCADAccess"
        push
        size="lg"
        v-close-popup
      />
      <q-btn
        color="primary"
        label="测试-CAD-文件集"
        @click="testCADFiles"
        push
        size="lg"
        v-close-popup
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      columns: [
        {
          name: "key",
          label: "键",
          align: "left",
          field: (row) => row.key,
          format: (val) => `${val}`,
        },
        { name: "value", align: "right", label: "值", field: "value" },
      ],
      data: [],
      date: "",
    };
  },
  methods: {
    testReset() {
      this.$reset();
    },
    testClear() {
      this.$clear();
    },
    testUpdate() {
      this.$update();
    },
    testParameterAll() {
      let log = this.$pall();
      console.log(JSON.parse(log));
      this.data = JSON.parse(log);
    },
    testParameterClear() {
      this.$pclear();
    },
    testParameterQuery() {
      this.$q
        .dialog({
          title: "测试",
          message: "输入查询项",
          prompt: {
            model: "",
            type: "text", // optional
          },
          cancel: true,
          persistent: true,
        })
        .onOk((data) => {
          console.log(data);
          this.$q.notify({
            message: window.AndroidWebView.ParameterQuery(data),
            type: "positive",
            position: "top",
          });
        });
    },
    testParameterDelete() {
      let that = this;
      this.$q
        .dialog({
          title: "测试",
          message: "输入删除项",
          prompt: {
            model: "",
            type: "text", // optional
          },
          cancel: true,
          persistent: true,
        })
        .onOk((data) => {
          that.$pdelete(data);
        });
    },
    testParameterInsert() {
      let that = this;
      this.$q
        .dialog({
          title: "测试",
          message: "输入插入项(逗号分隔k,v)",
          prompt: {
            model: "",
            type: "text", // optional
          },
          cancel: true,
          persistent: true,
        })
        .onOk((data) => {
          that.$pinsert(data.split(",")[0], data.split(",")[1]);
        });
    },
    testParameterUpdate() {
      let that = this;
      this.$q
        .dialog({
          title: "测试",
          message: "输入修改项(逗号分隔k,v)",
          prompt: {
            model: "",
            type: "text", // optional
          },
          cancel: true,
          persistent: true,
        })
        .onOk((data) => {
          that.$pupdate(data.split(",")[0], data.split(",")[1]);
        });
    },
    testCachePut() {
      var date = new Date();
      var time = date.toLocaleTimeString();
      var log = window.AndroidWebView.CachePut("cache-" + time);
      document.getElementById("testLog").value = log;
    },
    testCacheGet() {
      var log = window.AndroidWebView.CacheGet(true);
      document.getElementById("testLog").value = log;
      // var index = log.indexOf("_");
      // id = log.substring(0, index);
    },
    testCacheSize() {
      var log = window.AndroidWebView.CacheSize();
      document.getElementById("testLog").value = log;
    },
    testCacheRemove() {
      var log = window.AndroidWebView.CacheRemove(0);
      document.getElementById("testLog").value = log;
    },
    testCacheClear() {
      var log = window.AndroidWebView.CacheClear();
      document.getElementById("testLog").value = log;
    },
    testCADAccess() {
      var log = window.AndroidWebView.CADAccess("sample.dwg");
      document.getElementById("testLog").value = log;
    },
    testCADFiles() {
      var log = window.AndroidWebView.CADFiles();
      document.getElementById("testLog").value = log;
    },
  },
  created() {
    this.date = this.$date.formatDate(Date.now(), "YYYY-MM-DD  HH:mm:ss Z");
  },
};
</script>

