<template>
  <div>
    <div class="q-pa-md">
      <div class="q-gutter-md">
        <q-input
          outlined
          v-model="positionCode"
          autocapitalize="off"
          label="位置码"
        >
        </q-input>
        <q-input
          outlined
          v-model="deviceCode"
          autocapitalize="off"
          label="设备码"
          @keyup.enter="addToList"
        >
          <template v-slot:after>
            <q-btn round dense flat icon="send" @click="addToList" />
          </template>
        </q-input>
      </div>
      <br />
      <q-card style="height: calc(100vh - 285px); overflow-y: auto">
        <!-- <q-scroll-area> -->
        <q-card-section>
          <simpleList v-show="items != ''" :Litems="items" />
        </q-card-section>
        <!-- </q-scroll-area> -->
      </q-card>
      <div class="q-pt-md row" v-if="$store.state.miniapp">
        <q-btn
          class="col"
          color="primary"
          icon="center_focus_weak"
          label="扫描位置码"
          @click="scanLoc"
        /><q-btn
          class="col q-ml-sm"
          color="primary"
          icon="center_focus_weak"
          label="扫描设备码"
          @click="scanDevice"
        />
      </div>
      <div class="q-pt-md column">
        <q-btn
          color="primary"
          icon="exit_to_app"
          label="入库"
          @click="makeStore"
        />
      </div>
    </div>
  </div>
</template>

<script>
import simpleList from "@/components/simpleList.vue";
// import contact from "@/components/contact.vue";

export default {
  components: {
    simpleList,
  },
  data() {
    return {
      items: [],
      active: true,
      deviceCode: "",
      positionCode: "",
    };
  },
  methods: {
    scanLoc() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.positionCode = resultStr;
        },
      });
    },
    scanDevice() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.deviceCode = resultStr;
          this.addToList();
        },
      });
    },
    addToList() {
      if (this.deviceCode != "") {
        let tmp = this.items.map((a) => a.unique_code);
        if (tmp.indexOf(this.deviceCode) === -1) {
          this.$http
            .post(`warehouseInOfSearch`, {
              identity_code: this.deviceCode,
              location_unique_code: this.positionCode,
            })
            .then((res) => {
              this.items.push({
                ...res.data.data,
                active: false,
                location_unique_code: this.positionCode,
              });
            })
            .catch(({ response }) => {
              this.$q.notify({
                type: "warning",
                position: "top",
                message: response.data.msg,
              });
            });
          this.deviceCode = "";
        } else {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: "此设备已经添加",
          });
        }
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: "设备码不能为空",
        });
      }
    },
    makeStore() {
      if (this.items != "" && this.items != []) {
        // this.$q
        //   .dialog({
        //     component: contact,
        //     parent: this,
        //   })
        //   .onOk((data) => {
        //     console.log(">>>> OK, received", data);
        let req = {
          datas: this.items,
          // datas: this.items.filter((a) => a.active),
          date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
        };
        console.log(req);
        this.$http.post("warehouseIn", req).then((res) => {
          this.$q.notify({
            type: "positive",
            position: "top",
            message: res.data.msg,
          });
        });
        this.items = [];
        // this.items = this.items.filter((a) => !a.active);
        // });
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: `无设备`,
        });
      }
    },
  },
};
</script>

