<template>
  <div>
    <div class="q-pa-md">
      <div class="q-gutter-md">
        <q-input
          outlined
          v-model="deviceCode"
          autocapitalize="off"
          label="设备码"
          @keyup.enter="addToList"
        >
          <template v-slot:after>
            <q-btn round dense flat icon="send" @click="addToList" />
          </template>
        </q-input>
      </div>
      <br />
      <q-card style="height: calc(100vh - 260px); overflow-y: auto">
        <q-card-section>
          <simpleList v-show="items != ''" :Litems="items" :slidable="false" />
        </q-card-section>
      </q-card>
      <div class="q-pt-md column">
        <q-btn
          color="primary"
          icon="mail"
          label="检修完成"
          @click="makeComplete"
        />
      </div>
    </div>
  </div>
</template>

<script>
import simpleList from "@/components/simpleList.vue";

export default {
  components: {
    simpleList,
  },
  data() {
    return {
      items: [],
      active: true,
      deviceCode: "",
    };
  },
  methods: {
    addToList() {
      if (this.deviceCode != "") {
        let tmp = this.items.map((a) => a.identity_code);
        if (tmp.indexOf(this.deviceCode) === -1) {
          this.$http
            .post(`overhaulOfSearch`, { identity_code: this.deviceCode })
            .then((res) => {
              this.items.push({
                ...res.data.data,
                active: false,
              });
            })
            .catch(({ response }) => {
              this.$q.notify({
                type: "warning",
                position: "top",
                message: response.data.msg,
              });
            });
          this.deviceCode = "";
        } else {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: "此设备已经添加",
          });
        }
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: "设备码不能为空",
        });
      }
    },
    makeComplete() {
      if (this.items.length !== 0) {
        let req = {
          identityCodes: this.items
            // .filter((a) => a.active)
            .map((b) => b.identity_code),
          date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
        };
        console.log(req);
        this.$http
          .post("completeOverhaul", req)
          .then((res) => {
            this.$q.notify({
              type: "positive",
              position: "top",
              message: res.data.msg,
            });
            this.items = [];
          })
          .catch(({ response }) => {
            this.$q.notify({
              type: "warning",
              position: "top",
              message: response.data.msg,
            });
          });
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: `无设备`,
        });
      }
    },
  },
};
</script>

