<template>
  <div>
    <q-header elevated>
      <q-toolbar>
        <!-- <q-btn
          v-if="!$store.state.miniapp"
          flat
          dense
          round
          to="/"
          aria-label="Menu"
          icon="menu"
        /> -->
        <q-btn
          size="sm"
          padding="xs"
          v-show="$route.name !== '首页'"
          flat
          @click="$router.go(-1)"
          icon="arrow_back_ios_new"
        />
        <q-toolbar-title class="text-h6"
          ><strong>{{ $route.name }}</strong>
        </q-toolbar-title>
        <q-btn
          v-if="$store.state.miniapp"
          flat
          icon="search"
          @click="showSearch"
        />

        <!-- <q-input
          v-if="!$store.state.miniapp"
          dark
          dense
          standout
          v-model="text"
          input-class="text-left"
          class="q-mr-md"
          @keyup.enter="showDetail"
        >
          <template v-slot:prepend>
            <q-btn round dense flat icon="center_focus_weak" @click="showQR" />
          </template>
          <template v-slot:append>
            <q-icon v-if="text === ''" name="search" />
            <q-icon
              v-else
              name="clear"
              class="cursor-pointer"
              @click="text = ''"
            />
          </template>
        </q-input> -->
        <div v-if="!$store.state.miniapp">
          <q-icon v-show="onLine" name="signal_wifi_0_bar" />
          <q-icon v-show="!onLine" name="signal_wifi_off" class="text-orange" />
        </div>
        <q-btn flat round dense>
          <q-icon name="more_vert" />
          <q-menu>
            <div class="row no-wrap q-pa-md">
              <div class="column" v-if="!$store.state.miniapp">
                <div class="text-h6 q-mb-md">应用设置</div>
                <q-toggle
                  v-model="$store.state.showFab"
                  label="启用浮动工具栏"
                />
                <q-toggle
                  v-model="$store.state.offlineMode"
                  label="强制在线模式"
                />
                <q-btn
                  color="primary"
                  label="更新应用"
                  @click="update"
                  push
                  size="sm"
                  v-close-popup
                />
              </div>

              <q-separator
                vertical
                inset
                class="q-mx-lg"
                v-if="!$store.state.miniapp"
              />

              <div class="column items-center">
                <q-avatar size="72px">
                  <img src="../statics/profile.svg" />
                </q-avatar>
                <div class="text-subtitle1 q-mt-md q-mb-xs">{{ username }}</div>
                <q-btn
                  color="primary"
                  label="退出登录"
                  to="/login"
                  @click="logout"
                  push
                  size="sm"
                  v-close-popup
                />
              </div>
            </div>
          </q-menu>
        </q-btn>
      </q-toolbar>
      <!-- <q-toolbar v-if="!$store.state.miniapp" inset>
        <q-btn
          size="sm"
