    </q-header>
  </div>
</template>

<script>
import jwt from "jsonwebtoken";
import detail from "@/components/detail.vue";
import qrcode from "@/components/qrcode.vue";
import config from "@/components/config.vue";

export default {
  name: "NavBar",
  mounted() {
    window.addEventListener("online", this.updateOnlineStatus);
    window.addEventListener("offline", this.updateOnlineStatus);
    let img = new Image();
    img.src = require("../statics/profile.svg");
    // console.log(require("../statics/profile.svg"));
  },
  created() {
    const token = localStorage.getItem("token");
    let restoken = jwt.decode(token);
    this.username = restoken.payload.nickname;
    this.$store.state.userInfo = restoken.payload;
  },
  data() {
    return { onLine: navigator.onLine, username: "", text: "" };
  },
  methods: {
    showSearch() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.$q
            .dialog({
              title: "搜索",
              message: "输入设备码",
              prompt: {
                model: `${resultStr}`,
                type: "text", // optional
              },
              ok: {
                label: "搜索",
              },
              position: "top",
              cancel: true,
              persistent: true,
            })
            .onOk((data) => {
              this.$q
                .dialog({
                  component: detail,
                  parent: this,
                  ucode: data,
                })
                .onOk(() => {})
                .onCancel(() => {})
                .onDismiss(() => {});
            });
        },
      });
    },
    update() {
      this.$q
        .dialog({
          component: config,
          parent: this,
        })
        .onOk(() => {});
    },
    showQR() {
      this.$q
        .dialog({
          component: qrcode,
          parent: this,
        })
        .onOk((e) => {
          this.text = e;
          this.showDetail();
          console.log("OK");
        })
        .onCancel(() => {
          console.log("Cancel");
        })
        .onDismiss(() => {
          console.log("Called on OK or Cancel");
        });
    },
    showDetail() {
      this.$q
        .dialog({
          component: detail,
          parent: this,
          ucode: this.text,
        })
        .onOk(() => {})
        .onCancel(() => {})
        .onDismiss(() => {});
    },
    updateOnlineStatus(e) {
      const { type } = e;
      this.onLine = type === "online";
    },

    logout() {
      this.$q.notify({
        message: "退出登录成功",
        position: "top",
        type: "positive",
      });
      localStorage.clear();
    },
  },
  beforeDestroy() {
    window.removeEventListener("online", this.updateOnlineStatus);
    window.removeEventListener("offline", this.updateOnlineStatus);
  },
};
</script>

