<template>
  <q-layout>
    <q-page-container>
      <q-page class="flex bg-image flex-center">
        <q-card :style="$q.screen.lt.sm ? { width: '80%' } : { width: '30%' }">
          <q-card-section>
            <q-avatar size="103px" class="absolute-center shadow-10">
              <img src="../statics/profile.svg" />
            </q-avatar>
          </q-card-section>
          <q-card-section>
            <div class="text-center q-pt-lg">
              <div class="col text-h6 ellipsis">登录</div>
            </div>
          </q-card-section>
          <q-card-section>
            <q-form class="q-gutter-md">
              <q-input
                autocapitalize="off"
                filled
                v-model="ld.username"
                label="账号"
                placeholder="请输入账号"
                lazy-rules
              />

              <q-input
                type="password"
                filled
                placeholder="请输入密码"
                v-model="ld.password"
                label="密码"
                lazy-rules
                @keydown.enter="login"
              />

              <div>
                <q-btn
                  label="登录"
                  type="button"
                  class="full-width"
                  color="primary"
                  @click="login"
                />
              </div>
              <div>
                <q-btn
                  style="margin-top: -10px"
                  label="前往注册"
                  flat
                  type="button"
                  class="full-width"
                  color="primary"
                  to="/regsiter"
                />
              </div>
              <div v-if="!$store.state.miniapp">
                <q-btn
                  style="margin-top: -20px"
                  label="修改项目地址"
                  flat
                  @click="update"
                  type="button"
                  size="md"
                  class="full-width"
                  color="primary"
                />
              </div>
            </q-form>
          </q-card-section>
        </q-card>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import jwt from "jsonwebtoken";
import config from "@/components/config.vue";

export default {
  data() {
    return {
      ld: {
        username: "",
        password: "",
      },
    };
  },
  created() {
    console.log(this.$route.params);
    if (this.$route.params) {
      this.ld.username = this.$route.params.account;
      this.ld.password = this.$route.params.password;
    }
  },
  methods: {
    login() {
      if (!navigator.onLine) {
        let tmp = localStorage.getItem("basicinfo");
        tmp = JSON.parse(tmp);
        let check = this.$bcrypt.compareSync(
          this.ld.password,
          tmp.accounts.find((user) => user.account == this.ld.username).password //hash 密文
        );
        this.$q.notify({
          message: check ? "离线Hash验证成功" : "离线Hash验证错误",
          type: check ? "positive" : "warning",
          position: "top",
        });
        if (check) {
          const token = jwt.sign(
            {
              aud: tmp.accounts.find((user) => user.account == this.ld.username)
                .nickname,
            },
            "test"
          );
          localStorage.setItem("token", token);
          this.$router.push("/");
        }
      } else {
        this.$q.loadingBar.start();
        this.$http
          .post("login", {
            account: this.ld.username,
            password: this.ld.password,
          })
          .then((res) => {
            console.log(res);
            this.$store.state.userLevel = res.data.data.account.rank.code;
            this.$q.loadingBar.stop();
            if (res.data.status == 200) {
              this.$q.notify({
                message: "登录成功",
                type: "positive",
                position: "top",
              });
              localStorage.setItem("token", res.data.data.jwt);
              if (!this.$in_miniapp) {
                this.$router.push("/");
              } else {
                this.$router.push("/");
                // this.$router.push("/scenetask/home");
              }
            } else {
              this.$q.notify({
                message: res.data.message,
                type: "warning",
                position: "top",
              });
            }
          })
          .catch((err) => {
            console.log(err);
            this.$q.notify({
              message: "登录请求失败",
              type: "warning",
              position: "top",
            });
          });
      }
      //   console.log(this.ld);
    },
    update() {
      this.$q
        .dialog({
          component: config,
          parent: this,
        })
        .onOk(() => {});
    },
  },
};
</script>


<style>
.bg-image {
  background-image: linear-gradient(135deg, #9468dc 0%, #5f65e6 100%);
}
</style>
