<template>
  <div class="q-pa-md">
    <div class="row">
      <q-input
        :disable="!is_tmptask"
        class="col-9"
        v-model="task_name"
        label="任务名称"
        dense
        outlined
      />
      <q-input
        :disable="!is_tmptask"
        class="col q-ml-xs"
        v-model="unit"
        label="单位"
        dense
        outlined
      />
    </div>
    <q-select
      class="q-mt-sm"
      dense
      outlined
      :disable="!is_tmptask"
      v-model="station"
      :options="stations"
      map-options
      options-dense
      option-value="unique_code"
      option-label="name"
      label="选择车站"
      emit-value
    />
    <q-input
      dense
      class="q-mt-sm"
      outlined
      label="选择任务日期"
      v-model="date"
      mask="date"
    >
      <template v-slot:append>
        <q-icon name="event" class="cursor-pointer">
          <q-popup-proxy
            ref="qDateProxy"
            transition-show="scale"
            transition-hide="scale"
          >
            <q-date v-model="date">
              <div class="row items-center justify-end">
                <q-btn v-close-popup label="确认" color="primary" flat />
              </div>
            </q-date>
          </q-popup-proxy>
        </q-icon>
      </template>
    </q-input>

    <div class="q-mt-sm row">
      <q-select
        :loading="loading_lv5s"
        class="col"
        dense
        outlined
        v-model="lv5"
        :options="lv5s"
        map-options
        options-dense
        option-value="id"
        option-label="nickname"
        label="选择现场职工"
        emit-value
      />
    </div>
    <q-card
      style="overflow-y: auto; height: calc(100vh - 345px)"
      class="q-my-md"
    >
      <q-card-section>
        <q-list v-if="items.length !== 0"
          ><q-item dense v-ripple v-for="(item, index) in items" :key="index">
            <q-item-section side top>
              <q-checkbox v-model="item.checked" />
            </q-item-section>

            <q-item-section>
              {{ item.maintain_location_code }}{{ item.crossroad_number }}
              <q-item-label>{{ item.identity_code }}</q-item-label>
              <q-item-label>
                {{ item.category_name }} {{ item.model_name }}
                {{ item.sub_model_name }}
              </q-item-label>
            </q-item-section>
          </q-item>
        </q-list>
        <div class="q-pt-sm column" v-else>
          <q-btn
            color="primary"
            icon="add"
            :disable="station == ''"
            :label="station != '' ? '添加' : '选择车站后添加设备'"
            @click="showParams = true"
          />
        </div>
      </q-card-section>
    </q-card>
    <div class="q-pt-sm column">
      <q-btn
        color="primary"
        icon="fact_check"
        label="分配任务"
        @click="makeArrange"
      />
    </div>
    <q-dialog v-model="showParams" full-height full-width>
      <q-card>
        <!-- <q-card-section>
          <div class="text-h6">搜索条件</div>
        </q-card-section> -->

        <q-card-actions align="right">
          <q-btn
            flat
            label="添加"
            color="primary"
            v-close-popup
            @click="addDevice"
          />
        </q-card-actions>
        <q-card-section class="q-pt-none"
          ><div class="row">
            <q-checkbox
              flat
              v-model="selected_crossroad"
              v-for="crossroad in crossroads"
              :key="crossroad.name"
              :val="crossroad.name"
              :label="crossroad.name"
              class="q-pt-sm col-4"
            />
          </div>
        </q-card-section>
      </q-card>
    </q-dialog>
  </div>
</template>

<script>
import addstationVue from "../../components/addstation.vue";
export default {
  data() {
    return {
      is_tmptask: true,
      search_category_unique_code: "",
      search_entire_model_unique_code: "",
      search_model_unique_code: "",
      user_workshop: "",
      loading_lv5s: true,
      showParams: false,
      station: "",
      stations: [],
      items: [],
      lv5: "",
      lv5s: [],
      date: "",
      task_name: "",
      unit: "",
      crossroads: [],
      selected_crossroad: [],
      columns: [
        {
          name: "station_name",
          label: "车站名称",
          align: "left",
          field: "station_name",
        },
        {
          name: "number",
          align: "center",
          label: "数量（点击可编辑）",
          field: "number",
        },
      ],
      data: [],
    };
  },
  created() {
    if (this.$route.params.id) {
      this.is_tmptask = false;
      this.$http.get(`checkPlan/${this.$route.params.id}`).then((res) => {
        console.log(res.data.data.check_plan);
        this.task_name = res.data.data.check_plan.with_check_project.name;
        this.unit = res.data.data.check_plan.unit;
        this.station = res.data.data.check_plan.with_station.name;
        this.items = res.data.data.check_plan.check_plan_entire_instances
          .filter((a) => a.is_use === 0)
          .map((a) => ({ ...a.entire_instance, checked: false }));
      });
    }
    this.user_workshop = this.$store.state.userInfo.workshop_unique_code;
    this.$http
      .get("stations", {
        params: {
          type: "STATION",
          parent_unique_code: this.user_workshop,
        },
      })
      .then((res) => {
        this.stations = res.data.data.stations;
      });
    this.$http
      .get(
        `accounts?rank=SceneWorkAreaCrew&workshop_unique_code=${this.user_workshop}`
      )
      .then((res) => {
        // let tmp = res.data.data.accounts.filter((a) => a.nickname);
        this.lv5s = res.data.data.accounts;
        this.loading_lv5s = false;
      });
  },
  watch: {
    station(e) {
      let tmp_station_name = this.stations.find((a) => a.unique_code === e)
        .name;
      this.$http
        .get(
          `entireInstanceForCreateTaskStationCheckOrder?maintain_station_name=${tmp_station_name}`
        )
        .then((res) => {
          this.crossroads = Object.entries(res.data.data.entire_instances).map(
            ([a, b]) => ({
              name: a,
              data: b,
            })
          );
        });
      // console.log(this.stations.find((a) => a.unique_code === e).name);
    },
  },
  methods: {
    addDevice() {
      let tmp = [];
      this.selected_crossroad.map((a) => {
        tmp.push(...this.crossroads.find((b) => b.name == a).data);
      });
      this.items = tmp.map((a) => ({ ...a, checked: false }));
    },
    addStation() {
      this.$q
        .dialog({
          component: addstationVue,
          parent: this,
        })
        .onOk(({ unique_code, name: station_name }) => {
          if (!this.data.some((a) => a.unique_code === unique_code)) {
            this.data.push({ unique_code, station_name, number: 0 });
          } else {
            this.$q.notify({
              message: "车站已添加",
              position: "top",
              type: "warning",
            });
          }
        });
    },
    handleModel(e) {
      this.search_category_unique_code = e.category_unique_code;
      this.search_entire_model_unique_code = e.entire_model_unique_code;
      this.search_model_unique_code = e.model_unique_code;
    },
    makeArrange() {
      let tmp = this.items.filter((a) => a.checked).map((b) => b.identity_code);
      let req = {};
      // req["principal_id_1"] = this.lv1 * 1;
      // req["principal_id_2"] = this.lv2 * 1;
      if (!this.is_tmptask) {
        req["check_plan_serial_number"] = this.$route.params.id;
      } else {
        req["station_unique_code"] = this.station;
      }
      req["principal_id_5"] = this.lv5 * 1;
      req["expiring_at"] = this.date.split("/").join("-");
      req["unit"] = this.unit;
      req["project"] = this.task_name;
      req["identity_codes"] = tmp;
      this.$http
        .post("taskStationCheckOrder", { ...req })
        .then((res) => {
          this.$n_success(res);
          if (this.$route.params.id) {
            this.is_tmptask = false;
            this.$http.get(`checkPlan/${this.$route.params.id}`).then((res) => {
              console.log(res.data.data.check_plan);
              this.task_name = res.data.data.check_plan.with_check_project.name;
              this.unit = res.data.data.check_plan.unit;
              this.station = res.data.data.check_plan.with_station.name;
              this.items = res.data.data.check_plan.check_plan_entire_instances
                .filter((a) => a.is_use === 0)
                .map((a) => ({ ...a.entire_instance, checked: false }));
            });
          }
        })
        .catch(this.$n_error);
    },
  },
};
</script>
<style lang="sass" >
.tasktable
  /* height or max-height is important */
  height: calc(100vh - 405px)

  .q-table__top,
  .q-table__bottom,
  thead tr:first-child th
    /* bg color is important for th; just specify one */
    background-color: white

  thead tr th
    position: sticky
    z-index: 1
  thead tr:first-child th
    top: 0

  /* this is when the loading indicator appears */
  &.q-table--loading thead tr:last-child th
    /* height of all previous header rows */
    top: 48px
</style>