<template>
  <div>
    <div class="q-pa-md">
      <div class="row q-col-gutter-xs">
        <q-card
          style="text-align: center; vertical-align: center"
          @click="showSearch"
          class="bg-grey-4 col-6"
        >
          <q-card-section class="fix-center">
            <q-icon name="search" style="font-size: 4rem" />
            <div class="text-h6">设备详情查询</div>
          </q-card-section>
          <q-card-section class="q-pt-none"> </q-card-section>
        </q-card>
        <q-card
          v-for="(nav, index) in navs"
          :key="index"
          style="text-align: center; vertical-align: center"
          :class="nav.class + ' col-6'"
          @click="navFn(nav.path)"
          v-show="
            !nav.hidden && (!$store.state.miniapp || !nav.hidden_inminiapp)
          "
        >
          <q-card-section class="fix-center">
            <q-icon :name="nav.icon" style="font-size: 4rem" />
            <div class="text-h6">{{ nav.name }}</div>
          </q-card-section>

          <q-card-section class="q-pt-none"> </q-card-section>
        </q-card>
      </div>
    </div>
  </div>
</template>

<script>
import detail from "@/components/detail.vue";
export default {
  data() {
    return {
      navs: [],
    };
  },
  created() {
    this.navs = this.$router.options.routes.filter(
      (route) =>
        route.path != "/login" && route.path != "/" && route.path != "/regsiter"
    );
  },
  methods: {
    showSearch() {
      this.$q
        .dialog({
          title: "搜索",
          message: "输入设备码",
          prompt: {
            model: ``,
            type: "text", // optional
          },
          ok: {
            label: "搜索",
          },
          position: "top",
          cancel: true,
          persistent: true,
        })
        .onOk((data) => {
          this.$q
            .dialog({
              component: detail,
              parent: this,
              ucode: data,
            })
            .onOk(() => {})
            .onCancel(() => {})
            .onDismiss(() => {});
        });
    },
    navFn(path) {
      if (path.indexOf(":id") !== 0) {
        this.$router.push(path.replace(":id", ""));
      } else {
        this.$router.push(path);
      }
    },
  },
};
</script>

