<template>
  <div>
    <div class="q-pa-md">
      <div>
        <q-card style="height: calc(100vh - 130px); overflow-y: auto">
          <q-card-section>
            <q-list bordered separator v-show="!tasks.length == 0">
              <q-item
                v-for="task in tasks"
                :key="task.id"
                clickable
                v-ripple
                @click="navTask(task.serial_number)"
              >
                <q-item-section>
                  {{ task.serial_number }}
                  <q-item-label>
                    {{ task.scene_workshop.name }}
                    {{ task.maintain_station.name }}
                    {{ task.work_area_by_unique_code.name }}
                  </q-item-label>
                  <q-item-label></q-item-label>
                </q-item-section>
                <q-item-section avatar>{{ task.status.name }}</q-item-section>
              </q-item>
            </q-list>
          </q-card-section>
        </q-card>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    // simpleList,
  },
  data() {
    return {
      tasks: [],
      deviceCode: "",
    };
  },
  created() {
    this.$http.post("taskList", { type: "NEW_STATION" }).then((res) => {
      this.tasks = res.data.data.data;
    });
  },
  methods: {
    navTask(id) {
      console.log(id);
      this.$router.push({
        path: `/task/list/${id}`,
      });
    },
  },
};
</script>

