import axios from 'axios'
import Vue from 'vue'
import Notify from 'quasar/src/plugins/Notify.js';
import { Plugins } from '@capacitor/core';

const { Storage } = Plugins;

Vue.prototype.$http = axios
if (process.env.NODE_ENV == 'development') {
    if (!window.AndroidWebView) {
        console.log('123456789')
        const transmit = Storage.get({ key: 'transmit_url' })
        const target = Storage.get({ key: 'target_url' })
        Promise.all([transmit, target]).then(([a, b]) => {
            console.log('aaaa', a, b)
            if (a.value === null && b.value === null) {
                axios.defaults.baseURL = '/api/pda';
            } else {
                if (a.value === '' || a.value === null || a.value === "null") {
                    axios.defaults.baseURL = `http://${b.value}/api/pda`
                } else {
                    axios.defaults.baseURL = `http://${a.value}/api/transmit?target=http://${b.value}/api/pda`;
                }
            }
        })
    }
}
else if (process.env.NODE_ENV == 'debug') {
    axios.defaults.baseURL = 'http://localhost:8000/api/pda';
}
else if (process.env.NODE_ENV == 'production') {
    if (!window.AndroidWebView) {
        const transmit = Storage.get({ key: 'transmit_url' })
        const target = Storage.get({ key: 'target_url' })
        Promise.all([transmit, target]).then(([a, b]) => {

            console.log('aaaa', a, b)
            if (a.value === null && b.value === null) {
                axios.defaults.baseURL = '/api/pda';
            } else {
                if (a.value === '' || a.value === null || a.value === "null") {
                    axios.defaults.baseURL = `http://${b.value}/api/pda`

                } else {
                    axios.defaults.baseURL = `http://${a.value}/api/transmit?target=http://${b.value}/api/pda`;
                }
            }
        })
    } else {
        let transmit_url = window.AndroidWebView.ParameterQuery('transmit_url')
        let target_url = window.AndroidWebView.ParameterQuery('target_url')
        if (!transmit_url) {
            // console.log(transmit_url, target_url)
            // axios.defaults.baseURL = 'https://maintain-group.zhongchengkeshi.com/api/transmit?target=http://test.hefei.zhongchengkeshi.com/api/pda';
            axios.defaults.baseURL = '/api/pda';
        } else {
            console.log(transmit_url, target_url)
            axios.defaults.baseURL = `http://${JSON.parse(transmit_url).value}/api/transmit?target=http://${JSON.parse(target_url).value}/api/pda`;
        }
    }
}

Vue.prototype.$n_error = function ({ response }) {
    Notify.create({
        type: "warning",
        position: "top",
        message: response.data.msg,
    });
}
Vue.prototype.$n_success = function (res) {
    Notify.create({
        type: "positive",
        position: "top",
        message: res.data.msg,
    });
}
// axios.interceptors.request.use(config => {
//     return config;
// }, err => {
//     Notify({ message: '请求超时!' });
//     return Promise.resolve(err);
// })
axios.interceptors.response.use(data => {
    if (data.data.status == 200) {
        return Promise.resolve(data);
    } else {
        Notify.create({
            type: "warning",
            position: "top",
            message: data.data.msg,
        })
        return Promise.reject(data);
    }
}, err => {
    return Promise.reject(err);
})