<template>
  <div>
    <div class="q-pa-md">
      <div class="q-gutter-md">
        <q-input
          outlined
          v-model="deviceCode"
          autocapitalize="off"
          label="设备码"
          @keyup.enter="addToList"
        >
          <template v-slot:after>
            <q-btn round dense flat icon="send" @click="addToList" />
          </template>
        </q-input>
      </div>
      <br />
      <q-card style="height: calc(100vh - 260px); overflow-y: auto">
        <q-card-section>
          <simpleList v-show="items != ''" :Litems="items" :slidable="false" />
        </q-card-section>
      </q-card>
      <div class="q-pt-md column">
        <q-btn
          color="primary"
          icon="mail"
          label="设备验收"
          @click="makeAccecpt"
        />
      </div>
    </div>
  </div>
</template>

<script>
import simpleList from "@/components/simpleList.vue";
import contact from "@/components/contact.vue";

export default {
  components: {
    simpleList,
  },
  data() {
    return {
      items: [],
      active: true,
      deviceCode: "",
    };
  },
  methods: {
    addToList() {
      if (this.deviceCode != "") {
        let tmp = this.items.map((a) => a.identity_code);
        if (tmp.indexOf(this.deviceCode) === -1) {
          this.$http
            .get(`entireInstanceByIdentityCode/${this.deviceCode}`)
            .then((res) => {
              this.items.push({
                ...res.data.data.entire_instance,
                active: false,
              });
            })
            .catch(({ response }) => {
              this.$q.notify({
                type: "warning",
                position: "top",
                message: response.data.msg,
              });
            });
          this.deviceCode = "";
        } else {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: "此设备已经添加",
          });
        }
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: "设备码不能为空",
        });
      }
    },
    makeAccecpt() {
      if (this.items.length !== 0) {
        this.$q
          .dialog({
            component: contact,
            parent: this,
          })
          .onOk((data) => {
            console.log(">>>> OK, received", data);
            let req = {
              identityCodes: this.items
                // .filter((a) => a.active)
                .map((b) => b.identity_code),
              ...data,
              date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
            };
            console.log(req);
            this.$http
              .post("checkDevice", req)
              .then((res) => {
                this.$q.notify({
                  type: "positive",
                  position: "top",
                  message: res.data.msg,
                });
                this.items = [];
              })
              .catch(({ response }) => {
                this.$q.notify({
                  type: "warning",
                  position: "top",
                  message: response.data.msg,
                });
              });
          });
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: `无设备`,
        });
      }
    },
  },
};
</script>

