<template>
  <div>
    <div class="q-pa-md">
      <div class="q-gutter-md">
        <div class="q-gutter-sm row">
          <q-radio
            class="col"
            dense
            v-model="mode"
            val="model"
            label="按种类型搜索"
          />
          <q-radio
            class="col"
            dense
            v-model="mode"
            val="loc"
            label="按位置搜索"
          />
        </div>
        <!-- <q-input
          outlined
          v-model="searchData"
          autocapitalize="off"
          label="设备唯一码"
          @keyup.enter="search"
        >
        </q-input> -->
        <div class="q-pt-md column">
          <q-btn color="primary" icon="search" label="搜索" @click="search" />
        </div>
        <div v-if="mode === 'loc'">
          <q-expansion-item label="按位置搜索">
            <q-input
              outlined
              v-model="search_loc"
              autocapitalize="off"
              label="位置编码"
              @keyup.enter="search"
            >
            </q-input>
            <Location @locChange="handleLoc" />
          </q-expansion-item>
        </div>
        <div v-if="mode === 'model'">
          <q-expansion-item label="按种类型搜索">
            <model @modelChange="handleModel" />
          </q-expansion-item>
        </div>
      </div>
      <div
        ref="scrollTargetRef"
        class="q-pa-md"
        style="max-height: calc(100vh - 280px); overflow: auto"
      >
        <q-infinite-scroll
          @load="appendSearch"
          :offset="200"
          :scroll-target="$refs.scrollTargetRef"
          :disable="state"
        >
          <SimpleList :Litems="items" :showDelete="false" :slidable="false" />
          <template v-slot:loading>
            <div class="row justify-center q-my-md">
              <q-spinner-dots color="primary" size="40px" />
            </div>
          </template>
        </q-infinite-scroll>
      </div>
    </div>
  </div>
</template>

<script>
import SimpleList from "../components/simpleList.vue";
// import Model from "../components/model.vue";
import Location from "@/components/location.vue";
import Model from "../components/model.vue";

export default {
  components: { SimpleList, Location, Model },
  data() {
    return {
      mode: "model",
      searchData: "",
      items: [],
      page: 1,
      state: false,
      search_category_unique_code: "",
      search_entire_model_unique_code: "",
      search_model_unique_code: "",
      search_loc: "",
    };
  },
  watch: {
    mode() {
      this.search_category_unique_code = "";
      this.search_entire_model_unique_code = "";
      this.search_model_unique_code = "";
      this.search_loc = "";
      this.state = false;
      this.page = 1;
      this.items = [];
      this.appendSearch();
    },
  },
  methods: {
    search() {
      this.page = 1;
      this.$http
        .get("entireInstances", {
          params: {
            category_unique_code: this.search_category_unique_code
              ? this.search_category_unique_code
              : null,
            entire_model_unique_code: this.search_entire_model_unique_code
              ? this.search_entire_model_unique_code
              : null,
            model_unique_code: this.search_model_unique_code
              ? this.search_model_unique_code
              : null,
            "location_unique_code[operator]": this.search_loc ? "like" : null,
            "location_unique_code[value]": this.search_loc
              ? this.search_loc + "%"
              : null,
            page: 1,
          },
        })
        .then((res) => {
          this.items = res.data.data.data;
          this.page += 1;
        })
        .catch(({ response }) => {
          console.log(response);
        });
    },
    handleLoc(e) {
      this.search_loc = e;
    },
    handleModel(e) {
      this.search_category_unique_code = e.category_unique_code;
      this.search_entire_model_unique_code = e.entire_model_unique_code;
      this.search_model_unique_code = e.model_unique_code;
    },
    appendSearch(index, done) {
      this.$http
        .get("entireInstances", {
          params: {
            category_unique_code: this.search_category_unique_code
              ? this.search_category_unique_code
              : null,
            entire_model_unique_code: this.search_entire_model_unique_code
              ? this.search_entire_model_unique_code
              : null,
            model_unique_code: this.search_model_unique_code
              ? this.search_model_unique_code
              : null,
            "location_unique_code[operator]": this.search_loc ? "like" : null,
            "location_unique_code[value]": this.search_loc
              ? this.search_loc + "%"
              : null,
            page: this.page,
          },
        })
        .then((res) => {
          console.log(res);
          if (res.data.data.data.length !== 0) {
            this.page += 1;
            this.items.push(...res.data.data.data);
            done();
          } else {
            this.state = true;
            done();
          }
        })
        .catch(({ response }) => {
          console.log(response);
        });
    },
  },
};
</script>

