<template>
  <div>
    <div class="q-pa-md">
      <div class="q-gutter-md">
        <q-input
          outlined
          v-model="deviceCode"
          autocapitalize="off"
          label="设备码"
          @keyup.enter="addToList"
        >
          <template v-slot:after>
            <q-btn round dense flat icon="send" @click="addToList" />
          </template>
        </q-input>
      </div>
      <br />
      <q-card style="height: calc(100vh - 215px); overflow-y: auto">
        <!-- <q-scroll-area> -->
        <q-card-section>
          <simpleList v-show="items != ''" :Litems="items" :slidable="false" />
        </q-card-section>
        <!-- </q-scroll-area> -->
      </q-card>
      <div class="q-pt-md row" v-if="$store.state.miniapp">
        <q-btn
          class="col"
          color="primary"
          icon="center_focus_weak"
          label="扫描设备码"
          @click="scanDevice"
        />
      </div>
      <div class="q-pt-md column">
        <q-btn
          color="primary"
          icon="delete_sweep"
          label="报废"
          @click="makeScrap"
        />
      </div>
    </div>
  </div>
</template>

<script>
import simpleList from "@/components/simpleList.vue";

export default {
  components: {
    simpleList,
  },
  data() {
    return {
      items: [],
      active: true,
      deviceCode: "",
    };
  },
  methods: {
    scanDevice() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.deviceCode = resultStr;
          this.addToList();
        },
      });
    },
    addToList() {
      if (this.deviceCode != "") {
        let tmp = this.items.map((a) => a.identity_code);
        if (tmp.indexOf(this.deviceCode) === -1) {
          this.$http
            .post(`scrapOfSearch`, { data: this.deviceCode })
            .then((res) => {
              this.items.push({
                ...res.data.data,
                active: false,
              });
            })
            .catch(({ response }) => {
              this.$q.notify({
                type: "warning",
                position: "top",
                message: response.data.msg,
              });
            });
          this.deviceCode = "";
        } else {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: "此设备已经添加",
          });
        }
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: "设备码不能为空",
        });
      }
    },
    makeScrap() {
      if (this.items.length !== 0) {
        let req = {
          // datas: this.items.filter((a) => a.active).map((b) => b.unique_code),
          datas: this.items.map((b) => b.identity_code),
          date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
        };
        console.log(req);
        this.$http
          .post("scrap", req)
          .then((res) => {
            this.$q.notify({
              type: "positive",
              position: "top",
              message: res.data.msg,
            });
            this.items = this.items.filter((a) => !a.active);
            this.items = [];
          })
          .catch(({ response }) => {
            console.log(response);
            this.$q.notify({
              type: "warning",
              position: "top",
              message: response.data.msg,
            });
          });
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: `无设备`,
        });
      }
    },
  },
};
</script>

