<template>
  <div>
    <q-list bordered separator>
      <q-item
        v-for="(item, index) in printlist"
        :key="index"
        v-ripple
        clickable
        @click="showDetail(item.identity_code)"
      >
        <q-item-section>
          {{ item.identity_code }} {{ item.category_name }}
          {{ item.model_name }} {{ item.made_at.split(" ")[0] }}
        </q-item-section>
      </q-item>
    </q-list>
  </div>
</template>

<script>
import detail from "@/components/detail.vue";
export default {
  name: "print",
  props: ["items"],
  computed: {
    printlist() {
      return this.items;
    },
  },
  data() {
    return {};
  },
  methods: {
    showDetail(id) {
      this.$q
        .dialog({
          component: detail,
          parent: this,
          ucode: id,
        })
        .onOk(() => {
          console.log("OK");
        })
        .onCancel(() => {
          console.log("Cancel");
        })
        .onDismiss(() => {
          console.log("Called on OK or Cancel");
        });
    },
  },
};
</script>

