<template>
  <div class="q-pa-none">
    <div class="row">
      <q-select
        class="col q-pr-xs"
        dense
        outlined
        v-model="contact.workshop_unique_code"
        :options="workshops"
        map-options
        options-dense
        @filter="filterWorkshop"
        @filter-abort="abortFilterFn"
        option-value="unique_code"
        option-label="name"
        label="车间"
        emit-value
      />
      <q-select
        class="col q-pl-xs"
        dense
        outlined
        v-model="contact.station_unique_code"
        :options="stations"
        map-options
        options-dense
        use-input
        hide-selected
        fill-input
        @filter="filterStation"
        @filter-abort="abortFilterFn"
        option-value="unique_code"
        option-label="name"
        label="车站"
        emit-value
      />
    </div>
  </div>
</template>

<script>
export default {
  props: [],
  data() {
    return {
      stationsRaw: [],
      workshops: null,
      stations: null,
      contact: {
        workshop_unique_code: "",
        station_unique_code: "",
      },
    };
  },
  watch: {
    "contact.workshop_unique_code": function (e) {
      this.$emit("workshop", e);
      this.contact.station_unique_code = "";
    },
    "contact.station_unique_code": function (e) {
      this.$emit(
        "station",
        this.stations.find((a) => a.unique_code == e)
      );
    },
  },
  methods: {
    abortFilterFn() {},
    filterWorkshop(val, update, abort) {
      if (this.stationsRaw.length === 0) {
        this.$http
          .get("maintains")
          .then((res) => {
            this.stationsRaw = res.data.data;
            update(() => {
              this.workshops = res.data.data;
            });
          })
          .catch((err) => {
            console.log(err);
            abort();
          });
      } else {
        update(() => {
          this.workshops = this.stationsRaw;
        });
      }
    },
    filterStation(val, update, abort) {
      if (this.stationsRaw.length === 0) {
        this.$http
          .get("maintains")
          .then((res) => {
            this.stationsRaw = res.data.data;
            update(() => {
              let tmpStations = [];
              if (this.contact.workshop_unique_code != "") {
                res.data.data
                  .filter(
                    (a) => a.unique_code === this.contact.workshop_unique_code
                  )
                  .map((a) => {
                    tmpStations.push(...a.subset);
                  });
              } else {
                res.data.data.map((a) => {
                  tmpStations.push(...a.subset);
                });
              }
              if (val === "") {
                this.stations = tmpStations;
              } else {
                this.stations = tmpStations.filter(
                  (v) => v.name.indexOf(val) > -1
                );
              }
            });
          })
          .catch((err) => {
            console.log(err);
            abort();
          });
      } else {
        update(() => {
          let tmpStations = [];
          if (this.contact.workshop_unique_code != "") {
            this.stationsRaw
              .filter(
                (a) => a.unique_code === this.contact.workshop_unique_code
              )
              .map((a) => {
                tmpStations.push(...a.subset);
              });
          } else {
            this.stationsRaw.map((a) => {
              tmpStations.push(...a.subset);
            });
          }
          if (val === "") {
            this.stations = tmpStations;
          } else {
            this.stations = tmpStations.filter((v) => v.name.indexOf(val) > -1);
          }
        });
      }
    },
  },
};
</script>

