<template>
  <div>
    <div class="q-pa-md">
      <div class="q-gutter-md">
        <q-input
          dense
          outlined
          v-model="entire_instance_identity_code"
          label="设备唯一码"
        >
          <template v-slot:append>
            <q-btn
              v-if="!$store.state.miniapp"
              round
              dense
              flat
              icon="center_focus_weak"
              @click="showQR('instance')"
            />
          </template>
        </q-input>
        <q-input dense outlined v-model="maintain_location_code" label="位置码">
          <template v-slot:append>
            <q-btn
              v-if="!$store.state.miniapp"
              round
              dense
              flat
              icon="center_focus_weak"
              @click="showQR('loc')"
            />
          </template>
        </q-input>
        <!-- <stations @station="handleStation" /> -->
        <q-card style="height: calc(100vh - 300px); overflow-y: auto">
          <q-card-section>
            <q-list bordered separator v-show="!histories.length == 0">
              <q-item
                v-for="history in histories"
                :key="history.id"
                clickable
                v-ripple
                @click="showDetail(history.entire_instance_identity_code)"
              >
                <q-item-section class="text-caption">
                  {{ history.entire_instance_identity_code }}
                  <q-item-label
                    >车站：{{ history.maintain_station_name }}</q-item-label
                  >
                </q-item-section>
                <q-item-section avatar class="text-caption">
                  安装位置：{{ history.maintain_location_code }}
                  <q-item-label class="text-grey">{{
                    history.updated_at
                  }}</q-item-label>
                </q-item-section>
              </q-item>
            </q-list>
          </q-card-section>
        </q-card>
        <div class="row" v-if="$store.state.miniapp">
          <q-btn
            class="col"
            color="primary"
            icon="center_focus_weak"
            label="扫描位置码"
            @click="scanLoc"
          /><q-btn
            class="col q-ml-sm"
            color="primary"
            icon="center_focus_weak"
            label="扫描设备码"
            @click="scanDevice"
          />
        </div>
        <div class="column">
          <q-btn
            color="primary"
            icon="share_location"
            label="设备定位"
            @click="makeLoc"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import stations from "@/components/stations.vue";
import qrcode from "@/components/qrcode.vue";
import detail from "@/components/detail.vue";

export default {
  components: {
    // stations,
  },
  data() {
    return {
      histories: [{ id: 1, name: "aaa" }],
      entire_instance_identity_code: "",
      maintain_location_code: "",
      station: "",
    };
  },
  created() {
    this.$http.get("stationInstallLocationRecords").then((res) => {
      this.histories = res.data.data.station_install_location_records;
    });
  },
  methods: {
    scanLoc() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.maintain_location_code = resultStr;
        },
      });
    },
    scanDevice() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.entire_instance_identity_code = resultStr;
        },
      });
    },
    handleStation(e) {
      console.log(e.name);
      this.station = e.name;
    },
    makeLoc() {
      if (this.entire_instance_identity_code && this.maintain_location_code) {
        this.$http
          .post("correctMaintainLocation", {
            entire_instance_identity_code: this.entire_instance_identity_code,
            maintain_station_name: this.station,
            maintain_location_code: this.maintain_location_code,
          })
          .then((res) => {
            this.$q.notify({
              position: "top",
              message: res.data.msg,
              type: "positive",
            });
            this.entire_instance_identity_code = "";
            this.maintain_location_code = "";
            return this.$http.get("stationInstallLocationRecords");
          })
          .then((res) => {
            this.histories = res.data.data.station_install_location_records;
          })
          .catch(this.$n_error);
      }
    },
    showDetail(item) {
      this.$q
        .dialog({
          component: detail,
          parent: this,
          ucode: item,
        })
        .onOk(() => {})
        .onCancel(() => {})
        .onDismiss(() => {});
    },
    showQR(type) {
      this.$q
        .dialog({
          component: qrcode,
          parent: this,
        })
        .onOk((e) => {
          if (type === "instance") {
            this.entire_instance_identity_code = e;
          } else {
            this.maintain_location_code = e;
          }
        });
    },
  },
};
</script>

