<template>
  <q-layout>
    <q-page-container>
      <q-page class="flex bg-image flex-center">
        <q-card :style="$q.screen.lt.sm ? { width: '80%' } : { width: '30%' }">
          <q-card-section>
            <q-avatar size="103px" class="absolute-center shadow-10">
              <img src="../statics/profile.svg" />
            </q-avatar>
          </q-card-section>
          <q-card-section>
            <div class="text-center q-pt-lg">
              <div class="col text-h6 ellipsis">注册</div>
            </div>
          </q-card-section>
          <q-card-section>
            <q-form class="q-gutter-md">
              <q-input
                dense
                autocapitalize="off"
                filled
                v-model="ld.account"
                label="账号"
                placeholder="请输入账号"
                lazy-rules
              />
              <q-input
                dense
                autocapitalize="off"
                filled
                v-model="ld.nickname"
                label="名称"
                placeholder="请输入名称"
                lazy-rules
              />
              <q-input
                dense
                type="password"
                filled
                placeholder="请输入密码"
                v-model="ld.password"
                label="密码"
                lazy-rules
                @keydown.enter="regsiter"
              />
              <Stations @workshop="handleWorkshop" @station="handleStation" />
              <workareas ref="workareas" @workarea="handleWorkarea" />
              <ranks @rank="handleRank" />
              <div>
                <q-btn
                  label="注册"
                  type="button"
                  class="full-width"
                  color="primary"
                  @click="regsiter"
                />
              </div>
              <div>
                <q-btn
                  style="margin-top: -10px"
                  label="已经注册，前往登录"
                  flat
                  type="button"
                  size="sm"
                  class="full-width"
                  color="primary"
                  to="/login"
                />
              </div>
              <div v-if="!$store.state.miniapp">
                <q-btn
                  style="margin-top: -20px"
                  label="修改IP地址"
                  flat
                  @click="update"
                  type="button"
                  size="sm"
                  class="full-width"
                  color="primary"
                />
              </div>
            </q-form>
          </q-card-section>
        </q-card>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import config from "@/components/config.vue";
import Stations from "@/components/stations.vue";
import workareas from "@/components/workareas.vue";
import ranks from "@/components/ranks.vue";
// import jwt from "jsonwebtoken";

export default {
  components: { Stations, workareas, ranks },
  data() {
    return {
      ld: {
        account: "",
        password: "",
        nickname: "",
        workshop_unique_code: "",
        station_unique_code: "",
        work_area_unique_code: "",
        rank: "",
      },
    };
  },
  methods: {
    handleWorkshop(e) {
      this.ld.workshop_unique_code = e;
      this.$refs.workareas.filterWorkareas(e);
    },
    handleStation(e) {
      this.ld.station_unique_code = e.unique_code;
    },
    handleWorkarea(e) {
      this.ld.work_area_unique_code = e;
    },
    handleRank(e) {
      this.ld.rank = e;
    },
    update() {
      this.$q
        .dialog({
          component: config,
          parent: this,
        })
        .onOk(() => {});
    },
    regsiter() {
      console.log(this.ld);
      this.$http
        .post("register", { ...this.ld })
        .then((res) => {
          this.$q.notify({
            message: res.data.msg,
            type: "positive",
            position: "top",
          });
          this.$router.push({ name: "登录", params: { ...this.ld } });
        })
        .catch(({ response }) => {
          console.log(response);
          this.$q.notify({
            message: response.data.msg,
            type: "warning",
            position: "top",
          });
        });
      console.log("注册成功");
    },
  },
};
</script>


<style>
.bg-image {
  background-image: linear-gradient(135deg, #9468dc 0%, #5f65e6 100%);
}
</style>
