import Vue from 'vue'

Vue.prototype.$init = function (e) {
    window.AndroidWebView.Init(e)
}
Vue.prototype.$reset = function () {
    window.AndroidWebView.Reset()
}
Vue.prototype.$clear = function () {
    window.AndroidWebView.Clear()
}
Vue.prototype.$update = function () {
    window.AndroidWebView.Update()
}

Vue.prototype.$pall = function () {
    return window.AndroidWebView.ParameterAll()
}
Vue.prototype.$pclear = function () {
    return window.AndroidWebView.ParameterClear()
}
Vue.prototype.$pquery = function (e) {
    return window.AndroidWebView.ParameterQuery(e)
}
Vue.prototype.$pdelete = function (e) {
    return window.AndroidWebView.ParameterDelete(e)
}
Vue.prototype.$pinsert = function (k, v) {
    return window.AndroidWebView.ParameterInsert(k, v)
}
Vue.prototype.$pupdate = function (k, v) {
    return window.AndroidWebView.ParameterUpdate(k, v)
}