<template>
  <q-dialog ref="dialog" @hide="onDialogHide" maximized @click="hide()">
    <q-card class="q-dialog-plugin">
      <qrcode-stream @decode="onDecode"></qrcode-stream>
    </q-card>
  </q-dialog>
</template>

<script>
export default {
  name: "detail",
  data() {
    return {
      expanded: false,
      receipt: {},
      detail: {},
      items: {},
    };
  },
  methods: {
    onDecode(e) {
      this.$emit("ok", e);
      this.$refs.dialog.hide();
    },
    show() {
      this.$refs.dialog.show();
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      this.$emit("ok");
      this.hide();
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

