<template>
  <div>
    <div class="q-pa-md">
      <div v-if="userRank === 'SceneWorkAreaCrew'">
        <q-card style="height: calc(100vh - 95px); overflow-y: auto">
          <q-card-section>
            <div class="col q-mb-sm">
              <div class="text-subtitle2">待完成任务列表</div>
            </div>
            <q-list bordered separator v-show="!data.length == 0">
              <q-item
                v-for="task in data"
                :key="task.id"
                clickable
                v-ripple
                @click="navTask(task.serial_number)"
              >
                <q-item-section>
                  {{ task.with_check_plan.with_check_project.name }}

                  <q-item-label>
                    {{ task.principal_id_level5.nickname }}
                    {{ task.expiring_at.split(" ")[0] }}</q-item-label
                  >
                  <q-item-label></q-item-label>
                </q-item-section>
              </q-item>
            </q-list>
          </q-card-section>
        </q-card>
      </div>
      <div v-else>
        <q-table
          :style="
            userRank === 'SceneWorkshopPrincipal'
              ? 'height: calc(100vh - 145px)'
              : 'height: calc(100vh - 100px)'
          "
          class="statstable"
          :data="data"
          :visibleColumns="visibleColumns"
          :columns="columns"
          row-key="name"
          binary-state-sort
          :pagination="{ rowsPerPage: 20 }"
          hide-bottom
        >
          <template v-slot:top>
            <div class="col q-table__title">年月表计划</div>
            <q-space />

            <q-select
              v-model="selectedMonth"
              outlined
              dense
              options-dense
              label="月份"
              :options="month"
            />
          </template>
          <template v-slot:body="props">
            <q-tr :props="props">
              <q-td
                key="project"
                :props="props"
                :style="{ width: '400px', whiteSpace: 'normal' }"
              >
                {{ props.row.with_check_project.name }}
              </q-td>
              <q-td key="maintain_station_unique_code" :props="props">
                {{ props.row.with_station.name }}
              </q-td>
              <!-- <q-td key="unit" :props="props">
                {{ props.row.unit }}
              </q-td> -->
              <q-td key="number" :props="props">
                <!-- <div class="row"> -->
                {{ props.row.number }}
                <!-- </div> -->
              </q-td>
              <q-td key="code" :props="props">
                <q-btn
                  v-if="props.row.count !== 0"
                  flat
                  round
                  icon="fact_check"
                  size="sm"
                  @click="check(props.row.serial_number, props.row)"
                />
              </q-td>
            </q-tr>
          </template>
        </q-table>
        <div class="q-pt-md column">
          <q-btn
            v-show="userRank === 'SceneWorkshopPrincipal'"
            color="primary"
            icon="assignment"
            label="创建临时任务"
            @click="createTask"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import jwt from "jsonwebtoken";
export default {
  components: {
    // simpleList,
  },
  data() {
    return {
      month: [
        "2021-01",
        "2021-02",
        "2021-03",
        "2021-04",
        "2021-05",
        "2021-06",
        "2021-07",
        "2021-08",
        "2021-09",
        "2021-10",
        "2021-11",
        "2021-12",
      ],
      selectedMonth: "2021-05",
      visibleColumns: [
        "project",
        "maintain_station_unique_code",
        "number",
        "code",
      ],
      columns: [
        {
          name: "project",
          label: "任务名称",
          align: "left",
          field: "project",
        },
        {
          name: "maintain_station_unique_code",
          label: "车站",
          align: "left",
          field: "maintain_station_unique_code",
        },
        {
          name: "number",
          align: "center",
          label: "计划设备数",
          field: "number",
        },
        // {
        //   name: "number",
        //   align: "center",
        //   label: "状态",
        //   field: "number",
        // },
        {
          name: "code",
          align: "center",
          label: "分配",
          field: "code",
        },
      ],
      data: [],
      tasks: [],
      deviceCode: "",
      userRank: "",
    };
  },
  created() {
    let token = localStorage.getItem("token");
    this.userRank = jwt.decode(token).payload.rank.code;
    let id = jwt.decode(token).payload.id;
    if (this.userRank === "SceneWorkAreaCrew") {
      this.$http
        .get(`taskStationCheckOrder?status=UNDONE&principal_id_level_5=${id}`)
        .then((res) => {
          this.data = res.data.data.task_station_check_orders;
        });
    } else {
      this.$http.get("checkPlan").then((res) => {
        this.data = res.data.data.check_plans;
      });
    }
  },
  methods: {
    navTask(id) {
      console.log(id);
      this.$router.push({
        path: `/scenetask/check/${id}`,
      });
    },
    createTask() {
      this.$router.push({
        path: `/scenetask/edit`,
      });
    },
    check(sn, row) {
      console.log(row);
      if (row.status.value === 3) {
        this.$q.notify({
          position: "top",
          type: "warning",
          message: "任务已分配完成",
        });
        return;
      }
      if (row.status.value === 1) {
        this.$q.notify({
          position: "top",
          type: "warning",
          message: "任务已结束",
        });
        return;
      }
      if (row.number === 0) {
        this.$q.notify({
          position: "top",
          type: "warning",
          message: "计划没有分配设备",
        });
        return;
      }
      this.$router.push({
        path: `/scenetask/edit/${sn}`,
      });
    },
  },
};
</script>

<style lang="sass" >
