  <div>
    <div class="q-pa-md q-gutter-md">
      <q-card flat bordered>
        <!-- <q-card-section class="text-h6"> 完成情况统计 </q-card-section>   -->
        <q-card-section class="q-my-xs q-py-xs">
          <q-select
            class="q-mt-sm"
