            options-dense
            option-value="id"
            option-label="name"
            label="项目名称"
            emit-value
          />
          <q-select
            class="q-mt-md"
            v-model="date"
            outlined
            dense
            options-dense
            label="月份"
            :options="month"
          />
        </q-card-section>
        <!-- <q-card-section class="q-my-xs q-py-xs" v-if="state === 2">
          车间:肥西高铁车间 车间主任:张三</q-card-section
        >
        <q-card-section class="q-my-xs q-py-xs" v-if="state === 3">
