        </q-card-section> -->
        <!-- <q-card-section class="text-h6">
          <q-input filled v-model="date['to']">
            <template v-slot:append>
              <q-icon name="event" class="cursor-pointer">
                <q-popup-proxy
                  ref="qDateProxy"
