        ></q-card-section> -->
      </q-card>
      <div class="row">
        <q-card
          :class="
            'col-4 ' +
            (item.finished / item.aggregate === 1
              ? 'bg-green-11'
              : item.finished / item.aggregate === 0
              ? 'bg-red-11'
              : 'bg-orange-11')
          "
          v-for="item in data"
          :key="item.name"
          @click="maintain_unique_code = item.unique_code"
        >
          <q-card-section class="text-h6">{{ item.name }}</q-card-section>
          <q-card-section>{{ item.project_name }}</q-card-section>
          <q-card-section
            >完成情况：{{ item.finished }}/{{ item.aggregate }}</q-card-section
          >
        </q-card>
