<script>
export default {
  components: {
    // simpleList,
  },
  data() {
    return {
      month: [
        "2021-01",
        "2021-02",
        "2021-03",
        "2021-04",
        "2021-05",
        "2021-06",
        "2021-07",
        "2021-08",
        "2021-09",
        "2021-10",
        "2021-11",
        "2021-12",
      ],
      state: 1,
      user_workshop: "",
      date: "2021-05",
      maintain_unique_code: "",
      projects: [],
      project: "",
      data: [],
    };
  },
  watch: {
    project() {
      this.render();
    },
    maintain_unique_code() {
      this.render();
    },
    date() {
      this.render();
    },
  },
  created() {
    this.$http.get("checkProject").then((res) => {
      this.projects = res.data.data.check_projects;
    });
    // this.user_workshop = this.$store.state.userInfo.workshop_unique_code;
    this.render();
  },
  methods: {
    render() {
      this.$http
        .get(`taskStationCheckStatisticForProject`, {
          params: {
            project_id: this.project,
            expiring_at: this.date,
            maintain_unique_code: this.maintain_unique_code,
          },
        })
        .then((res) => {
          this.data = res.data.data.mission_statistic.map((a) => ({
            ...a,
            finished:
              res.data.data.finish_statistic.find(
                (b) =>
                  b.unique_code === a.unique_code &&
                  b.project_id === a.project_id
              )?.aggregate ?? 0,
          }));
        });
    },
  },
};
</script>