<template>
  <div>
    <div class="q-pa-md">
      <div class="q-gutter-md">
        <q-input
          outlined
          v-model="deviceCode"
          autocapitalize="off"
          label="设备码"
          @keyup.enter="addToList"
        >
          <template v-slot:after>
            <q-btn round dense flat icon="send" @click="addToList" />
          </template>
        </q-input>
      </div>
      <br />
      <q-card style="height: calc(100vh - 215px); overflow-y: auto">
        <!-- <q-scroll-area> -->
        <q-card-section>
          <simpleList
            v-show="items != ''"
            :Litems="items"
            :showDelete="showD"
            :slidable="false"
          />
        </q-card-section>
        <!-- </q-scroll-area> -->
      </q-card>
      <div class="q-pt-md row" v-if="$store.state.miniapp">
        <q-btn
          class="col"
          color="primary"
          icon="center_focus_weak"
          label="扫描设备码"
          @click="scanDevice"
        />
      </div>
      <div v-if="showD" class="q-pt-md column">
        <q-btn
          color="primary"
          icon="unarchive"
          label="出所"
          @click="makeDepotOut"
        />
      </div>
      <div v-else class="q-pt-md column">
        <q-btn
          color="primary"
          icon="mail"
          label="出所"
          @click="makeTaskDepotOut"
        />
      </div>
    </div>
  </div>
</template>

<script>
import simpleList from "@/components/simpleList.vue";
import contact from "@/components/contact.vue";

export default {
  components: {
    simpleList,
  },
  data() {
    return {
      items: [],
      active: true,
      deviceCode: "",
      showD: true,
    };
  },
  created() {
    if (this.$route.params.id) {
      this.showD = false;
      this.$http
        .post("workshopStayOutEntireInstances", {
          serial_number: this.$route.params.id,
        })
        .then((res) => {
          let tmp = res.data.data;
          tmp.map((a) => {
            this.items.push({ ...a.entire_instance, active: false });
          });
        });
    }
  },
  methods: {
    scanDevice() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.deviceCode = resultStr;
          this.addToList();
        },
      });
    },
    addToList() {
      if (this.showD === false) {
        let tmpRes = this.items.find(
          (a) => a.identity_code === this.deviceCode
        );
        console.log(tmpRes);
        if (tmpRes) {
          this.$q.notify({
            type: "positive",
            position: "top",
            message: "扫码成功",
          });
          tmpRes.active = true;
          this.deviceCode = "";
        } else {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: "不存在此设备",
          });
        }
        return;
      }
      if (this.deviceCode != "") {
        let tmp = this.items.map((a) => a.identity_code);
        if (tmp.indexOf(this.deviceCode) === -1) {
          this.$http
            .post(`workshopOutOfSearch`, { data: this.deviceCode })
            .then((res) => {
              this.items.push({
                ...res.data.data,
                active: false,
              });
            })
            .catch(({ response }) => {
              this.$q.notify({
                type: "warning",
                position: "top",
                message: response.data.msg,
              });
            });
          this.deviceCode = "";
        } else {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: "此设备已经添加",
          });
        }
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: "设备码不能为空",
        });
      }
    },
    makeDepotOut() {
      if (this.items != "" && this.items != []) {
        this.$q
          .dialog({
            component: contact,
            parent: this,
          })
          .onOk((data) => {
            console.log(">>>> OK, received", data);
            let req = {
              datas: this.items
                // .filter((a) => a.active)
                .map((b) => b.identity_code),
              ...data,
              date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
            };
            console.log(req);
            this.$http
              .post("workshopOut", req)
              .then((res) => {
                this.$q.notify({
                  type: "positive",
                  position: "top",
                  message: res.data.msg,
                });
                // this.items = this.items.filter((a) => !a.active);
                this.items = [];
              })
              .catch(({ response }) => {
                this.$q.notify({
                  type: "warning",
                  position: "top",
                  message: response.data.msg,
                });
              });
          });
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: `无设备`,
        });
      }
    },
    makeTaskDepotOut() {
      if (this.items != "" && this.items != []) {
        this.$q
          .dialog({
            component: contact,
            parent: this,
          })
          .onOk((data) => {
            console.log(">>>> OK, received", data);
            let req = {
              serial_number: this.$route.params.id,
              identity_codes: this.items
                .filter((a) => a.active)
                .map((b) => b.identity_code),
              ...data,
              date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
            };
            console.log(req);
            this.$http
              .post("passWorkshopOut", req)
              .then((res) => {
                this.$q.notify({
                  type: "positive",
                  position: "top",
                  message: res.data.msg,
                });
                this.items = this.items.filter((a) => !a.active);
              })
              .catch(({ response }) => {
                this.$q.notify({
                  type: "warning",
                  position: "top",
                  message: response.data.msg,
                });
              });
          });
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: `无设备`,
        });
      }
    },
  },
};
</script>

