<template>
  <div class="q-pa-none">
    <div class="row">
      <q-select
        class="col"
        dense
        outlined
        v-model="rank_code"
        :options="ranks"
        map-options
        options-dense
        option-value="unique_code"
        option-label="name"
        label="职务"
        emit-value
      />
    </div>
  </div>
</template>

<script>
export default {
  props: [],
  data() {
    return {
      ranks: [],
      rank_code: "",
    };
  },
  watch: {
    rank_code: function (e) {
      this.$emit("rank", e);
    },
  },
  created() {
    this.$http.get("ranks").then((res) => {
      this.ranks = Object.entries(res.data.data.ranks).map((a) => ({
        name: a[1],
        unique_code: a[0],
      }));
      this.rank_code = "None";
    });
  },
  methods: {
    abortFilterFn() {},
  },
};
</script>

