<template>
  <q-dialog ref="dialog" @hide="onDialogHide">
    <q-card class="q-dialog-plugin">
      <q-card-section class="row items-center q-pb-none">
        <div class="text-h6">添加车站</div>
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>
      <q-separator />
      <q-card-section style="max-height: 85%" class="scroll">
        <div class="q-gutter-md">
          <q-select
            dense
            outlined
            v-model="station"
            :options="stations"
            map-options
            options-dense
            option-value="unique_code"
            option-label="name"
            label="选择车站"
            emit-value
          />
        </div>
      </q-card-section>
      <q-card-actions align="right">
        <q-btn color="primary" label="确认" @click="onOKClick" />
        <q-btn color="primary" label="取消" @click="onCancelClick" />
      </q-card-actions>
    </q-card>
  </q-dialog>
</template>

<script>
export default {
  name: "config",
  data() {
    return {
      stations: [],
      station: "",
    };
  },
  methods: {
    show() {
      let tmp = this.$store.state.userInfo.workshop_unique_code;
      this.$http
        .get("stations", {
          params: {
            type: "STATION",
            parent_unique_code: tmp,
          },
        })
        .then((res) => {
          this.stations = res.data.data.stations;
          this.$refs.dialog.show();
        });
      this.$refs.dialog.show();
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      let find_res = this.stations.find((a) => a.unique_code === this.station);
      this.$emit("ok", find_res);
      this.hide();
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

