<template>
  <div class="q-pa-md">
    <div class="">
      <div style="height: calc(100vh - 125px)" class="q-gutter-md">
        <q-btn-toggle
          v-model="mode"
          spread
          no-caps
          toggle-color="blue"
          color="white"
          text-color="black"
          :options="[
            { label: '室内上道', value: 'in' },
            { label: '室外上道', value: 'out' },
          ]"
        />
        <q-input
          dense
          outlined
          v-model="install.identity_code"
          autocapitalize="off"
          label="待上道设备编号"
          @keyup.enter="getNew"
        />
        <stations
          @station="
            (e) => {
              install.station_unique_code = e.unique_code;
            }
          "
        />
        <q-input
          dense
          outlined
          v-model="install.maintain_location_code"
          autocapitalize="off"
          label="组合位置"
          v-if="mode == 'in'"
        >
        </q-input>
        <q-input
          dense
          outlined
          v-model="install.crossroad_number"
          autocapitalize="off"
          label="道岔号"
          v-if="mode == 'out'"
        >
        </q-input>
        <q-select
          v-if="mode == 'out'"
          dense
          options-dense
          outlined
          label="开向"
          v-model="install.open_direction"
          :options="options"
          emit-value
          map-options
        ></q-select>
        <device type="new" :item="newdevice" />
      </div>
      <!-- <div class="q-pt-md row" v-if="$store.state.miniapp">
        <q-btn
          class="col"
          color="primary"
          label="扫描已上道设备"
          @click="scanOldDevice"
        /><q-btn
          v-if="mode !== 'direct'"
          class="col q-ml-sm"
          color="primary"
          label="扫描待上道设备"
          @click="scanNewDevice"
        />
      </div> -->
      <div class="q-pt-none column">
        <q-btn
          color="primary"
          icon="file_upload"
          label="上道"
          @click="makeInstall"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Device from "@/components/device.vue";
import stations from "@/components/stations.vue";
export default {
  components: { Device, stations },
  data() {
    return {
      shape: "line",
      mode: "in",
      install: {
        identity_code: "",
        maintain_location_code: "",
        crossroad_number: "",
        open_direction: "",
        station_unique_code: "",
      },
      olddevice: {},
      newdevice: {},
      group: "op1",
      direction: "",
      options: [
        {
          label: "左",
          value: "left",
        },
        {
          label: "右",
          value: "right",
        },
        {
          label: "无",
          value: "none",
        },
      ],
    };
  },
  watch: {
    mode() {
      this.install.maintain_location_code = "";
      this.install.crossroad_number = "";
    },
  },
  methods: {
    scanOldDevice() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.install.old_identity_code = resultStr;
          this.getOld();
        },
      });
    },
    scanNewDevice() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.install.new_identity_code = resultStr;
          this.getNew();
        },
      });
    },
    makeInstall() {
      let req = {
        // type: this.mode,
        date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
        ...this.install,
      };
      this.$http
        .post(
          `outDoorInstalledUnStrictAndInDoorInstalledStrict/${this.install.identity_code}`,
          req
        )
        .then((res) => {
          this.$q.notify({
            type: "positive",
            position: "top",
            message: res.data.msg,
          });
        })
        .catch(({ response }) => {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: response.data.msg,
          });
        });
    },
    getOld() {
      this.$http
        .get("installed", {
          params: {
            code: this.install.identity_code,
          },
        })
        .then((res) => {
          if (res.data.data.length !== 0) {
            this.olddevice = res.data.data[0];
            this.install.old_identity_code = "";
          } else {
            this.$q.notify({
              type: "warning",
              position: "top",
              message: "设备不存在",
            });
          }
        });
    },
    getNew() {
      this.$http
        .get("installed", {
          params: {
            code: this.install.identity_code,
          },
        })
        .then((res) => {
          if (res.data.data.length !== 0) {
            this.newdevice = res.data.data[0];
            this.install.new_identity_code = "";
          } else {
            this.$q.notify({
              type: "warning",
              position: "top",
              message: "设备不存在",
            });
          }
        });
    },
  },
};
</script>

