<template>
  <div class="q-pa-md">
    <q-btn-group style="height: 3.6rem" push spread>
      <q-btn
        push
        label="分配检修"
        @click="
          $router.push({
            path: `/task/arrange/${$route.params.id}`,
            query: { work_area_unique_code: data[0].workAreaUniqueCode },
          })
        "
        icon="timeline"
      />
      <q-btn
        push
        label="现场退回"
        @click="$router.push(`/task/return/${$route.params.id}`)"
        icon="visibility"
      />
      <q-btn
        push
        label="检修完成"
        @click="$router.push(`/task/complete/${$route.params.id}`)"
        icon="check"
      />
      <q-btn
        push
        label="设备验收"
        @click="$router.push(`/task/acceptance/${$route.params.id}`)"
        icon="update"
      />
    </q-btn-group>
    <br />
    <q-table
      :title="`新站任务单：${$route.params.id}`"
      :data="data"
      :columns="columns"
      row-key="workshop"
      hide-bottom
    />
    <q-linear-progress
      rounded
      size="35px"
      :value="progress"
      color="green"
      class="q-mt-sm"
    >
      <div class="absolute-full flex flex-center">
        <q-badge color="white" text-color="black" :label="progressLabel" />
      </div>
    </q-linear-progress>
    <br />
    <q-card class="q-pt-none">
      <q-tabs
        v-model="tab"
        dense
        class="text-grey"
        active-color="primary"
        indicator-color="primary"
        align="justify"
        narrow-indicator
      >
        <q-tab name="tasks" label="待出所单" />
        <q-tab name="devices" label="设备列表" />
      </q-tabs>

      <q-separator />

      <q-tab-panels v-model="tab" animated>
        <q-tab-panel name="tasks">
          <div class="text-h6">待出所单</div>
          <q-card-section style="height: calc(100vh - 520px); overflow-y: auto">
            <q-list bordered separator v-show="taskdetails.length !== 0">
              <q-item
                v-for="task in taskdetails"
                :key="task.id"
                clickable
                v-ripple
                @click="navTask(task.serial_number)"
              >
                <q-item-section>{{ task.serial_number }}</q-item-section>
                <!-- <q-item-section avatar>{{ task }}</q-item-section> -->
              </q-item>
            </q-list>
          </q-card-section>
        </q-tab-panel>

        <q-tab-panel name="devices">
          <q-card-section style="height: calc(100vh - 520px); overflow-y: auto">
            <simpleList
              v-show="items != ''"
              :Litems="items"
              :slidable="false"
              :showDelete="false"
            />
          </q-card-section>
        </q-tab-panel>
      </q-tab-panels>
    </q-card>
  </div>
</template>

<script>
import simpleList from "@/components/simpleList.vue";
export default {
  components: { simpleList },
  data() {
    return {
      tab: "tasks",
      columns: [
        {
          name: "workshop",
          required: true,
          label: "车间",
          align: "left",
          field: (row) => row.workshopName,
          format: (val) => `${val}`,
          sortable: false,
        },
        {
          name: "station",
          align: "left",
          label: "车站",
          field: "stationName",
          sortable: false,
        },
        {
          name: "area",
          align: "left",
          label: "工区",
          field: "workAreaName",
          sortable: false,
        },
        {
          name: "deadline",
          align: "left",
          label: "截止日期",
          field: "expiringAt",
          sortable: false,
        },
      ],
      items: [],
      data: [],
      taskdetails: [],
      progress: 0,
      total: 0,
      finished: 1,
    };
  },
  computed: {
    progressLabel() {
      return "任务进度：" + this.finished + "/" + this.total;
    },
  },
  created() {
    this.$http
      .post("workshopStayOut", {
        // 获取待出所单列表
        serial_number: this.$route.params.id,
      })
      .then((res) => {
        if (res.data.data.length === 0) {
          this.tab = "devices";
        }
        this.taskdetails = res.data.data;
      });
    this.$http
      .post("listDetails", {
        serial_number: this.$route.params.id,
      })
      .then((res) => {
        this.data = [res.data.data];
        this.progress =
          res.data.data.workshopOutCount / res.data.data.taskOrderCount; //计算任务进度
        this.total = res.data.data.taskOrderCount;
        this.finished = res.data.data.workshopOutCount;
        this.items = res.data.data.entire_instances.data.map((a) => {
          if (a.is_scene_back.code === 1) {
            a.entire_instance.status = "现场退回";
          }
          if (a.is_out === 1) {
            a.entire_instance.active = true;
          }
          return a.entire_instance;
        });
      });
  },
  methods: {
    navTask(id) {
      this.$router.push({
        path: `/depotout/${id}`,
        query: { work_area_unique_code: this.data[0].workAreaUniqueCode },
      });
    },
  },
};
</script>

