<template>
  <div class="q-pa-md q-gutter-sm">
    <q-tree
      :nodes="locations"
      node-key="unique_code"
      label-key="name"
      @update:selected="selectaaa"
      :selected.sync="selected"
      children-key="subset"
    />
    {{ selected }}
  </div>
</template>

<script>
export default {
  name: "location",
  data() {
    return {
      selected: "",
      locations: [],
    };
  },
  mounted() {
    this.loadLocations();
  },
  methods: {
    selectaaa(e) {
      console.log(e);
      this.$emit("locChange", e);
    },
    loadLocations() {
      const notif = this.$q.notify({
        type: "ongoing",
        message: "正在获取位置数据...",
        position: "top",
      });
      this.$http
        .get("locations")
        .then((res) => {
          this.locations = res.data.data;
          notif({
            message: "获取位置信息成功",
            position: "top",
            type: "positive",
          });
        })
        .catch(({ response }) => {
          notif({
            message: response.data.msg,
            position: "top",
            type: "warning",
          });
        });
    },
  },
};
</script>

