<template>
  <q-layout view="lHh Lpr lFf">
    <NavBar v-if="$route.name != '登录' && $route.name != '注册'" />
    <q-page-container style="flex: 1">
      <Fab
        v-show="
          $route.name != '登录' && $route.name != '注册' && $store.state.showFab
        "
        style="z-index: 90"
      />
      <router-view></router-view>
    </q-page-container>
  </q-layout>
</template>

<script>
import Fab from "./components/Fab.vue";
import NavBar from "./components/NavBar.vue";

export default {
  name: "LayoutDefault",

  components: {
    Fab,
    NavBar,
  },
  created() {
    this.$wx.miniProgram.getEnv((res) => {
      if (res.miniprogram) {
        document.title = "合肥电务段";
        console.log("aaaa");
        this.$store.state.miniapp = true;
        this.$http
          .get(
            `https://test.hefei.zhongchengkeshi.com/api/wechatMiniApp/jsApiSignature?url=${
              location.href.split("#")[0]
            }`
          )
          .then((res) => {
            console.log("bbb");
            let {
              noncestr: nonceStr,
              timestamp,
              signature,
              app_id: appId,
            } = res.data.data;
            this.$wx.config({
              debug: false,
              appId,
              timestamp,
              nonceStr,
              signature,
              jsApiList: ["scanQRCode", "chooseImage", "getLocalImgData"],
            });
          });
      }
    });
  },
};
</script>

