<template>
  <q-dialog ref="dialog" @hide="onDialogHide" maximized>
    <q-card class="q-dialog-plugin">
      <q-card-section class="row items-center q-pb-none">
        <div class="text-h6">电子签名</div>
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>
      <q-separator />
      <q-card-section style="max-height: 100%">
        <sign-canvas
          class="sign-canvas sign-image"
          ref="SignCanvas"
          :options="options"
      /></q-card-section>
      <q-card-actions align="right">
        <q-btn color="primary" label="确认" @click="onOKClick" />
        <q-btn color="primary" label="取消" @click="onCancelClick" />
      </q-card-actions>
    </q-card>
  </q-dialog>
</template>

<script >
import SignCanvas from "sign-canvas";
export default {
  name: "contact",
  components: {
    SignCanvas,
  },
  data() {
    return {
      options: {
        isDpr: false, //是否使用dpr兼容高倍屏 [Boolean] 可选
        lastWriteSpeed: 1, //书写速度 [Number] 可选
        lastWriteWidth: 2, //下笔的宽度 [Number] 可选
        lineCap: "round", //线条的边缘类型 [butt]平直的边缘 [round]圆形线帽 [square]	正方形线帽
        lineJoin: "bevel", //线条交汇时边角的类型  [bevel]创建斜角 [round]创建圆角 [miter]创建尖角。
        canvasWidth: 320, //高度  [Number] 可选
        canvasHeight: 200, //高度  [Number] 可选
        isShowBorder: false, //是否显示边框 [可选]
        bgColor: "#F2F2F2", //背景色 [String] 可选
        borderWidth: 1, // 网格线宽度  [Number] 可选
        borderColor: "black", //网格颜色  [String] 可选
        writeWidth: 5, //基础轨迹宽度  [Number] 可选
        maxWriteWidth: 30, // 写字模式最大线宽  [Number] 可选
        minWriteWidth: 5, // 写字模式最小线宽  [Number] 可选
        writeColor: "#101010", // 轨迹颜色  [String] 可选
        isSign: true, //签名模式 [Boolean] 默认为非签名模式,有线框, 当设置为true的时候没有任何线框
        imgType: "png", //下载的图片格式  [String] 可选为 jpeg  canvas本是透明背景的
      },
    };
  },
  methods: {
    show() {
      this.$refs.dialog.show();
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      const res = this.$refs.SignCanvas.saveAsImg();
      this.$emit("ok", res);
      this.hide();
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

