import Vue from 'vue'
import httpBuildQuery from 'http-build-query'
import md5 from 'md5'

Vue.prototype.$sign = (obj, secret_key) => {
    let tmp = {}
    let obj2 = Object.assign({}, obj)
    obj2['secret_key'] = secret_key;
    let tmp2 = Object.keys(obj2)
    tmp2.sort()
        .map((key) => {
            if (obj2[key]) {
                tmp[key] = obj2[key];
            }
        });
    return md5(httpBuildQuery(tmp)).toUpperCase()
}