<template>
  <q-dialog ref="dialog" @hide="onDialogHide">
    <q-card class="q-dialog-plugin">
      <q-card-section class="row items-center q-pb-none">
        <div class="text-h6">选择检修人</div>
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>
      <q-separator />
      <q-card-section style="max-height: 85%" class="scroll">
        <div class="q-gutter-md">
          <q-select
            filled
            v-model="contact.account_id"
            :options="personnels"
            label="检修人"
            option-value="id"
            option-label="nickname"
            emit-value
            map-options
          />
        </div>
      </q-card-section>
      <q-card-actions align="right">
        <q-btn color="primary" label="确认" @click="onOKClick" />
        <q-btn color="primary" label="取消" @click="onCancelClick" />
      </q-card-actions>
    </q-card>
  </q-dialog>
</template>

<script>
export default {
  name: "contact",
  data() {
    return {
      personnels: [],
      contact: {
        account_id: "",
      },
    };
  },
  methods: {
    show() {
      this.$http
        .post("personnel", {
          work_area_unique_code: this.$route.query.work_area_unique_code,
        })
        .then((res) => {
          this.personnels = res.data.data;
        });
      this.$refs.dialog.show();
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      this.$emit("ok", { ...this.contact });
      this.hide();
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

