<template>
  <div>
    <img v-if="imgUrl" :src="imgUrl" alt="分享图片" />
    <div v-else id="qrcode" ref="box">
      <div class="text-h6">{{ qrcode_content }}</div>
    </div>
    <canvas ref="can" height="80" width="200"></canvas>
    <q-btn label="print" @click="generateImg"></q-btn>
  </div>
</template>

<script>
import { qrcanvas } from "qrcanvas";
// import html2canvas from "html2canvas";
export default {
  data() {
    return { imgUrl: "", qrcode_content: "Q040" };
  },
  mounted() {
    let that = this;
    that.appSrc = "www.baicu.com";
    that.$nextTick(function () {
      //生成二维码
      this.$refs.can = qrcanvas({
        data: decodeURIComponent(that.qrcode_content),
        size: 80,
      });
      //   var context = canvas1.getContext("2d");
      //   console.log(context);
      //   context.fillText("text", 40, 40);
      //   context.font = "48px serif";
      //   console.log(context);
      //   document.getElementById("qrcode").innerHTML = "";
      //   document.getElementById("qrcode").prepend(canvas1);

      // 合成分享图
    });
  },
  methods: {
    generateImg() {
      setTimeout(() => {
        let dataURL = this.$refs.can.toDataURL("image/png");
        window.AndroidWebView.Printing(
          35,
          20,
          1,
          0,
          0,
          2.1,
          dataURL.split(",")[1]
        );
        // console.log(dataURL.split(",")[1]);
      }, 0);
      //   html2canvas(this.$refs.box).then(function (canvas) {
      //     let dataURL = canvas.toDataURL("image/png");

      //   });
    },
  },
};
</script>

