<template>
  <q-dialog ref="dialog" position="top" @hide="onDialogHide">
    <q-card class="q-dialog-plugin">
      <q-card-section class="row items-center q-pb-none">
        <div class="text-h6">配置网络</div>
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>
      <q-separator />
      <q-card-section style="max-height: 85%" class="scroll">
        <div class="q-gutter-md">
          <q-input
            outlined
            v-model="transmit"
            label="转发地址(不使用转发请不填)"
            dense
          />
          <q-input outlined v-model="target" label="目标地址" dense />
        </div>
      </q-card-section>
      <q-card-actions align="right">
        <q-btn color="primary" label="确认" @click="onOKClick" />
        <q-btn color="primary" label="取消" @click="onCancelClick" />
      </q-card-actions>
    </q-card>
  </q-dialog>
</template>

<script>
import { Plugins } from "@capacitor/core";

const { Storage } = Plugins;

export default {
  name: "config",
  data() {
    return {
      transmit: "",
      target: "",
    };
  },
  methods: {
    async getStorage() {
      this.transmit = (await Storage.get({ key: "transmit_url" })).value;
      console.log(this.transmit);
      this.target = (await Storage.get({ key: "target_url" })).value;
    },
    show() {
      this.getStorage();
      this.$refs.dialog.show();
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      const transmit = Storage.set({
        key: "transmit_url",
        value: this.transmit,
      });
      const target = Storage.set({ key: "target_url", value: this.target });
      Promise.all([transmit, target]).then(() => {
        this.$emit("ok");
        this.hide();
        if (
          this.transmit == "" ||
          this.transmit == null ||
          this.transmit == "null"
        ) {
          this.$http.defaults.baseURL = `http://${this.target}/api/pda`;
        } else {
          this.$http.defaults.baseURL = `http://${this.transmit}/api/transmit?target=http://${this.target}/api/pda`;
        }
      });
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

