<template>
  <div class="q-pa-md">
    <div class="q-gutter-md">
      <q-input
        dense
        :disable="mode === 'direct'"
        outlined
        v-model="unique_code"
        autocapitalize="off"
        label="上道设备/器材唯一编码"
        @keyup.enter="getNew"
        autofocus
      />
      <q-input
        ref="level"
        dense
        outlined
        v-model="levelCode"
        autocapitalize="off"
        label="上道位置层编码"
        @keydown.enter="getPosition"
      >
      </q-input>
      <device :item="newdevice" />
      <position :position="position_detail" @positionChange="test" />
      <div class="q-pt-md column">
        <q-btn
          color="primary"
          icon="file_upload"
          label="上道"
          @click="makeInstall"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Device from "@/components/device.vue";
import position from "@/components/position.vue";
// import installContactVue from "../../components/installContact.vue";
export default {
  components: { Device, position },
  data() {
    return {
      shape: "line",
      mode: "cycle",
      install: {
        new_identity_code: "",
        old_identity_code: "",
        maintain_location_code: "",
      },
      unique_code: "",
      olddevice: {},
      newdevice: {},
      levelCode: "",
      position_detail: {},
      position: "",
    };
  },
  methods: {
    test(e) {
      this.position = e;
    },
    makeInstall() {
      // this.$q
      //   .dialog({
      //     component: installContactVue,
      //     parent: this,
      //   })
      //   .onOk((detail) => {
      let req = {
        status: "INSTALLED",
        date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
        maintain_location_code: this.position,
        // ...detail,
      };
      this.$http
        .post(`inDoorInstalledStrictAndInstalling/${this.unique_code}`, req)
        .then((res) => {
          this.$q.notify({
            type: "positive",
            position: "top",
            message: res.data.msg,
          });
        })
        .catch(({ response }) => {
          console.log(response);
          this.$q.notify({
            type: "warning",
            position: "top",
            message: response.data.msg,
          });
        });
      // })
      // .onCancel(() => {})
      // .onDismiss(() => {});
    },
    getNew() {
      this.$http
        .get(`installed`, { params: { code: this.unique_code } })
        .then((res) => {
          this.newdevice = res.data.data[0];
          this.$q.notify({
            type: "positive",
            position: "top",
            message: res.data.msg,
          });
          this.$refs.level.focus();
        })
        .catch(({ response }) => {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: response.data.msg,
          });
        });
    },
    getPosition() {
      this.$http
        .get(`installPosition/byInstallTierUniqueCode/${this.levelCode}`)
        .then((res) => {
          this.$q.notify({
            type: "positive",
            position: "top",
            message: res.data.msg,
          });
          this.position_detail = {
            ...res.data.data,
            install_tier_unique_code: this.levelCode,
          };
        })
        .catch(this.$n_error);
    },
  },
};
</script>

