<template>
  <q-dialog ref="dialog" @hide="onDialogHide">
    <q-card class="q-dialog-plugin">
      <q-card-section class="row items-center q-pb-none">
        <div class="text-h6">补充联系人信息</div>
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>
      <q-separator />
      <q-card-section style="max-height: 85%" class="scroll">
        <div class="q-gutter-md">
          <q-input
            outlined
            disable
            v-model="$store.state.userInfo.nickname"
            label="经办人"
          />
          <!-- <q-input outlined v-model="contact.contact_name" label="联系人姓名" /> -->
          <q-input
            outlined
            v-model="contact.contact_phone"
            label="联系人电话"
          />
        </div>
        <q-btn
          v-if="!contact.sign_img"
          color="primary"
          class="q-mt-md full-width"
          label="电子签名"
          @click="sign"
        />
        <q-img
          class="q-mt-md"
          style="height: 200px"
          v-else
          contain
          :src="contact.sign_img"
          @click="sign"
        ></q-img>
      </q-card-section>
      <q-card-actions align="right">
        <q-btn color="primary" label="确认" @click="onOKClick" />
        <q-btn color="info" label="取消" @click="onCancelClick" />
      </q-card-actions>
    </q-card>
  </q-dialog>
</template>

<script>
import sign from "../components/sign.vue";
export default {
  name: "contact",
  data() {
    return {
      contact: {
        contact_name: "",
        contact_phone: "",
        sign_img: "",
      },
    };
  },
  methods: {
    sign() {
      this.$q
        .dialog({
          component: sign,
          parent: this,
        })
        .onOk((e) => {
          this.contact.sign_img = e;
        })
        .onCancel(() => {
          console.log("Cancel");
        })
        .onDismiss(() => {
          console.log("Called on OK or Cancel");
        });
    },
    show() {
      this.$refs.dialog.show();
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      this.$emit("ok", { ...this.contact });
      this.hide();
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

