<template>
  <q-dialog ref="dialog" @hide="onDialogHide" full-height persistent>
    <q-card class="q-dialog-plugin">
      <q-tabs
        v-model="tab"
        dense
        class="bg-primary text-white relative"
        indicator-color="purple"
        align="justify"
      >
        <q-tab name="detail" label="设备详情" />
        <q-tab name="history" label="设备履历" />
        <q-tab
          v-if="instance['state_code'] === 'INSTALLED'"
          name="loc"
          label="现场备品定位"
        />
      </q-tabs>

      <q-tab-panels v-model="tab" animated swipeable>
        <q-tab-panel
          name="detail"
          style="overflow-y: auto; height: calc(100vh - 160px)"
        >
          <div class="text-h6">设备详情</div>
          <q-item
            dense
            class="ellipsis"
            v-for="(entry, index) in render"
            :key="index"
          >
            {{ entry[0] }}:<q-space />
            {{
              instance[entry[1]] !== "http://iot.api.in3km.com"
                ? instance[entry[1]]
                : ""
            }}
          </q-item>
          <!-- <div class="q-pa-md q-gutter-md">
            <q-btn push color="teal" label="仓库位置定位" @click="toggle" />
            <q-img
              v-if="url !== null"
              :src="url"
              :ratio="1"
              class="q-mt-md"
              style="width: 320px"
            />
          </div> -->
        </q-tab-panel>

        <q-tab-panel name="history">
          <div class="text-h6">设备履历</div>

          <q-timeline
            color="primary"
            style="overflow-y: auto; height: calc(100vh - 170px)"
          >
            <q-timeline-entry
              v-for="log in logs"
              :key="log.date"
              :title="log.name"
              :subtitle="log.created_at.split(' ')[0]"
            >
              <div>{{ log.description }}</div>
            </q-timeline-entry>
          </q-timeline>
        </q-tab-panel>

        <q-tab-panel v-if="instance['state_code'] === 'INSTALLED'" name="loc">
          <div class="text-h6">现场备品定位</div>
          <q-item
            >所属车间车站:{{ loc[0]["name"] }}，{{ loc[1]["name"] }}</q-item
          >
          <q-item>备品数量:{{ loc[0]["count"] }}，{{ loc[1]["count"] }}</q-item>
        </q-tab-panel>
      </q-tab-panels>

      <q-card-actions v-if="tab === 'detail'" align="right">
        <!-- <q-btn color="primary" label="确认" @click="onOKClick" /> -->
        <q-btn color="primary" label="关闭" @click="onCancelClick" />
      </q-card-actions>
    </q-card>
  </q-dialog>
</template>

<script>
export default {
  name: "detail",
  props: ["ucode"],
  data() {
    return {
      url: null,
      instance: {},
      logs: [],
      loc: [],
      tab: "detail",
      render: [
        ["唯一编码", "identity_code"],
        ["所编号", "serial_number"],
        ["种类", "category_name"],
        ["类型", "model_name"],
        ["型号", "sub_model_name"],
        ["使用年限", "service_life"],
        ["到期时间", "scarping_at"],
        ["供应商", "factory_name"],
        ["出厂日期", "made_at"],
        ["仓库位置", "location_name"],
        ["安装位置", "maintain_location_code"],
        ["状态", "status"],
        ["车间", "maintain_workshop_name"],
        ["车站", "maintain_station_name"],
      ],
    };
  },
  computed: {
    unique_code() {
      return this.ucode;
    },
  },
  methods: {
    toggle() {
      this.url =
        this.url === null
          ? `${window.location.protocol}//${window.location.host}` +
            this.instance.location_image
          : null;
    },
    show() {
      let id = this.unique_code;
      this.$http
        .get(`entireInstanceByIdentityCode/${id}`)
        .then((res) => {
          this.instance = res.data.data.entire_instance;
          // let tmp = Object.values(res.data.data.material_logs);
          this.logs = res.data.data.entire_instance.entire_instance_logs;
          // this.loc = Object.values(res.data.data.standbies);
          this.$refs.dialog.show();
        })
        .catch(({ response }) => {
          this.$q.notify({
            message: response.data.msg,
            type: "warning",
            position: "top",
          });
        });
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      this.$emit("ok");
      this.hide();
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

