<template>
  <div>
    <div class="q-pa-md">
      <div class="q-gutter-md">
        <q-input
          outlined
          :disable="state"
          :dense="state"
          v-model="positionCode"
          autocapitalize="off"
          label="盘点位置"
          @keyup.enter="getCheckList"
        >
          <template v-if="!state" v-slot:after>
            <q-btn round dense flat icon="send" @click="getCheckList" />
          </template>
        </q-input>
        <q-input
          outlined
          dense
          v-if="state"
          v-model="deviceCode"
          autocapitalize="off"
          label="盘点设备码"
          @keyup.enter="preCheck"
        >
          <template v-slot:after>
            <q-btn round dense flat icon="send" @click="preCheck" />
          </template>
        </q-input>
      </div>
      <br />
      <q-card
        :style="
          state
            ? 'height: calc(100vh - 255px);'
            : 'height: calc(100vh - 215px);' + 'overflow-y: auto; '
        "
      >
        <q-card-section>
          <simpleList
            v-if="items.length !== 0"
            :Litems="items"
            :showDelete="!state"
            :slidable="false"
          />
        </q-card-section>
      </q-card>
      <div class="q-pt-md row" v-if="$store.state.miniapp">
        <q-btn
          v-if="!state"
          class="col"
          color="primary"
          icon="center_focus_weak"
          label="扫描位置码"
          @click="scanLoc"
        /><q-btn
          v-else
          class="col"
          color="primary"
          icon="center_focus_weak"
          label="扫描设备码"
          @click="scanDevice"
        />
      </div>
      <div class="q-pt-md column">
        <q-btn
          color="primary"
          icon="fact_check"
          label="盘点"
          @click="makeCheck"
        />
      </div>
    </div>
  </div>
</template>

<script>
import simpleList from "@/components/simpleList.vue";

export default {
  components: {
    simpleList,
  },
  data() {
    return {
      state: false,
      items: [],
      active: true,
      deviceCode: "",
      positionCode: "",
    };
  },
  methods: {
    scanLoc() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.positionCode = resultStr;
          this.getCheckList();
        },
      });
    },
    scanDevice() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.deviceCode = resultStr;
          this.preCheck();
        },
      });
    },
    getCheckList() {
      this.$http
        .post(`takeStock/ready`, { location_unique_code: this.positionCode })
        .then((res) => {
          let tmp = res.data.data;
          tmp.map((item) => {
            this.items.push({ ...item, active: false });
          });
          this.state = true;
        })
        .catch(({ response }) => {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: response.data.msg,
          });
        });
    },
    preCheck() {
      let findRes = this.items.find((a) => a.identity_code === this.deviceCode);
      if (findRes) {
        this.$q.notify({
          type: "positive",
          position: "top",
          message: "盘点正常",
        });
        findRes.active = true;
        findRes.check = "盘点正常";
        console.log(this.items);
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: "盘盈",
        });
        this.$http
          .post(`takeStock/scanCode`, {
            location_unique_code: this.positionCode,
            identity_code: this.deviceCode,
          })
          .then((res) => {
            console.log(res);
            this.items.push({
              ...res.data.data,
              check: "盘盈",
              active: true,
            });
          });
      }
    },
    // addToList() {
    //   if (this.deviceCode != "") {
    //     let tmp = this.items.map((a) => a.unique_code);
    //     if (tmp.indexOf(this.deviceCode) === -1) {
    //       this.$http
    //         .post(`out/scan`, {
    //           location_unique_code: this.positionCode,
    //           identity_code: this.deviceCode,
    //         })
    //         .then((res) => {
    //           this.items.push({
    //             ...res.data.data,
    //             active: false,
    //           });
    //         })
    //         .catch(({ response }) => {
    //           this.$q.notify({
    //             type: "warning",
    //             position: "top",
    //             message: response.data.msg,
    //           });
    //         });
    //       this.deviceCode = "";
    //     } else {
    //       this.$q.notify({
    //         type: "warning",
    //         position: "top",
    //         message: "此设备已经添加",
    //       });
    //     }
    //   } else {
    //     this.$q.notify({
    //       type: "warning",
    //       position: "top",
    //       message: "设备码不能为空",
    //     });
    //   }
    // },
    makeCheck() {
      if (this.items != "" && this.items != []) {
        let req = {
          location_unique_code: this.positionCode,
          identity_code: this.items
            .filter((a) => a.active)
            .map((b) => b.identity_code),
          date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
        };
        console.log(req);
        this.$http
          .post("takeStock", req)
          .then((res) => {
            this.$q.notify({
              type: "positive",
              position: "top",
              message: res.data.msg,
            });
            this.items = [];
          })
          .catch(({ response }) => {
            console.log(response);
            this.$q.notify({
              type: "warning",
              position: "top",
              message: response.data.msg,
            });
          });
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: `无设备`,
        });
      }
    },
  },
};
</script>

