<template>
  <div class="q-pa-md q-gutter-sm">
    <q-tree
      :nodes="stations"
      node-key="unique_code"
      label-key="name"
      children-key="subset"
      @update:selected="selectaaa"
      :selected.sync="selected"
      default-expand-all
    />
  </div>
</template>

<script>
export default {
  name: "station",
  data() {
    return {
      stations: [],
      selected: "",
    };
  },
  mounted() {
    this.loadStations();
  },
  methods: {
    selectaaa(e) {
      console.log(e);
    },
    loadStations() {
      const notif = this.$q.notify({
        type: "ongoing",
        message: "正在获取车站数据...",
        position: "top",
      });
      this.$http
        .get("maintains")
        .then((res) => {
          this.stations = res.data.data;
          notif({
            message: "获取车站信息成功",
            position: "top",
            type: "positive",
          });
        })
        .catch(({ response }) => {
          notif({
            message: response.data.msg,
            position: "top",
            type: "warning",
          });
        });
    },
  },
};
</script>

