<template>
  <div class="q-pa-none">
    <div class="row">
      <q-select
        class="col"
        dense
        outlined
        v-model="work_area_unique_code"
        :options="workareas"
        map-options
        options-dense
        option-value="unique_code"
        option-label="name"
        label="工区"
        emit-value
      />
    </div>
  </div>
</template>

<script>
export default {
  props: [],
  data() {
    return {
      workareas: [],
      workareasRaw: [],
      work_area_unique_code: "",
    };
  },
  watch: {
    work_area_unique_code: function (e) {
      this.$emit("workarea", e);
    },
  },
  created() {
    this.$http.get("workAreas").then((res) => {
      this.workareas = res.data.data.work_areas;
      this.workareasRaw = res.data.data.work_areas;
    });
  },
  methods: {
    abortFilterFn() {},
    filterWorkareas(workshop_unique_code) {
      this.work_area_unique_code = "";
      this.workareas = this.workareasRaw.filter(
        (a) => a.workshop_unique_code === workshop_unique_code
      );
      console.log(this.workareas);
    },
  },
};
</script>

