<template>
  <div class="q-pa-md">
    <q-card flat bordered>
      <q-card-section class="text-h6" style="height: 64px">
        {{ taskDetail.with_check_plan.with_check_project.name }}
        {{ taskDetail.expiring_at.split(" ")[0] }}
      </q-card-section>
      <q-card-section>
        <div class="text-grey">任务进度</div>
        <q-linear-progress
          size="40px"
          :value="progress"
          color="green"
          class="q-mt-md"
        >
          <div class="absolute-full flex flex-center">
            <q-badge
              color="white"
              text-color="blue"
              :label="`${taskDetail.task_station_check_entire_instances_count}/${taskDetail.number}`"
            />
          </div>
        </q-linear-progress>
      </q-card-section>
    </q-card>
    <!-- <q-input
      v-if="userRank === 'SceneWorkAreaCrew'"
      class="q-pt-md"
      outlined
      v-model="deviceCode"
      autocapitalize="off"
      label="设备码"
      @keyup.enter="addToList"
    >
      <template v-slot:after>
        <q-btn round dense flat icon="send" @click="addToList" />
      </template>
    </q-input> -->

    <q-card
      :style="
        'overflow-y: auto;' +
        (userRank === 'SceneWorkAreaCrew'
          ? 'height: calc(100vh - 325px)'
          : 'height: calc(100vh - 285px)')
      "
      class="q-mt-md"
    >
      <!-- <q-scroll-area> -->
      <q-card-section>
        <simpleList
          v-show="items != ''"
          :Litems="items"
          :showDelete="false"
          :showCrossroad="true"
          :slidable="false"
          @delete="handleDelete"
        />
      </q-card-section>
      <!-- </q-scroll-area> -->
    </q-card>
    <div class="q-pt-md row" v-if="userRank === 'SceneWorkAreaCrew'">
      <q-btn
        class="col"
        color="primary"
        icon="center_focus_weak"
        label="扫描设备码"
        @click="scanDevice"
      />
    </div>
  </div>
</template>

<script>
import simpleList from "@/components/simpleList.vue";
import addphoto from "@/components/addphoto.vue";
import jwt from "jsonwebtoken";

export default {
  components: {
    simpleList,
  },
  data() {
    return {
      userRank: "",
      deviceCode: "",
      progress: 0,
      taskDetail: {},
      items: [],
    };
  },
  async created() {
    await this.init_data();
    let token = localStorage.getItem("token");
    this.userRank = jwt.decode(token).payload.rank.code;
  },
  methods: {
    handleDelete(e) {
      console.log(e);
      this.$http
        .delete(`taskStationCheckEntireInstance/${this.$route.params.id}`, {
          data: {
            entire_instance_identity_code: e,
          },
        })
        .then((res) => {
          this.$n_success(res);
          this.init_data();
        })
        .catch(this.$n_error);
    },
    scanDevice() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.deviceCode = resultStr;
          this.addToList();
        },
      });
    },
    addToList() {
      if (this.deviceCode != "") {
        console.log(this.items);
        let tmp = this.items.find((a) => {
          return a.identity_code === this.deviceCode;
        });
        // let tmp_index = this.items.findIndex((a) => {
        //   return a.identity_code === this.deviceCode;
        // });
        if (!tmp) {
          this.$q.notify({
            message: "设备未找到",
            position: "top",
            type: "warning",
          });
          return;
        }
        this.$q
          .dialog({
            component: addphoto,
            parent: this,
          })
          .onOk((data) => {
            console.log(JSON.stringify([data]));
            this.$http
              .post(`taskStationCheckEntireInstance/${this.$route.params.id}`, {
                entire_instance_identity_code: this.deviceCode,
                images: JSON.stringify([data]),
              })
              .then((res) => {
                this.$n_success(res);
                this.init_data();
                this.deviceCode = "";
              })
              .catch(({ response }) => {
                this.$q.notify({
                  type: "warning",
                  position: "top",
                  message: response.data.msg,
                });
              });
          })
          .onCancel(() => {
            console.log("bbbb");
            this.$http
              .post(`taskStationCheckEntireInstance/${this.$route.params.id}`, {
                entire_instance_identity_code: this.deviceCode,
                images: ``,
              })
              .then((res) => {
                this.$n_success(res);
                this.init_data();
                this.deviceCode = "";
              })
              .catch(({ response }) => {
                this.$q.notify({
                  type: "warning",
                  position: "top",
                  message: response.data.msg,
                });
              });
          });
      } else {
        this.$q.notify({
          type: "warning",
          position: "top",
          message: "设备码不能为空",
        });
      }
    },
    async init_data() {
      let status = await this.$http.get(
        `taskStationCheckOrder/${this.$route.params.id}`
      );
      let { task_station_check_order } = status.data.data;
      this.taskDetail = task_station_check_order;
      this.items = task_station_check_order.task_station_check_entire_instances.map(
        (a) => {
          if (a.processed_at) {
            a.entire_instance.active = true;
          }
          return a.entire_instance;
        }
      );
      this.progress =
        task_station_check_order.task_station_check_entire_instances_count /
        task_station_check_order.number;
    },
    check() {
      this.$wx.scanQRCode({
        needResult: 1,
        scanType: ["qrCode"],
        success: (res) => {
          let { resultStr } = res;
          this.deviceCode = resultStr;
          this.addToList();
        },
      });
    },
  },
};
</script>
